<template>
    <div class="perlinnoise">
        <div id="perlinnoise_body">
            <div id="perlinnoise_content">
                <div id="te_can">
                    <h1>柏林噪声</h1>
                    <canvas id="canvas4" width="2000" height="200"></canvas>
                </div>
                <!-- <p>本来在看贝塞尔曲线文章的时候就发现了柏林噪声算法，但是由于这个好像比较复杂，所以先去做了贝塞尔</p>
                <p>现在回头一看这个是真的头大，网上中文文章数量很少，大家讲的原理在细节上不尽相同，还有几篇说别人做的是错的...都不知道要信哪个，不过在阅读了很多机翻外网文章后，似乎找到了一些头绪，希望下面做的没有太多问题</p> -->
                <h2>伪随机与差值</h2>
                <p>如果在水平轴每隔一个单位有一个点，其纵坐标为随机值，那么大概是这样的场景，这其实就是一种噪声</p>
                <div style="overflow:hidden">
                    <canvas id="canvas5" width="1500" height="300"></canvas>
                </div>
                <p>感觉是混乱中毫无美感，总不能有人说有种星空的美丽吧。那有没有一种可能——通过不断刷新让它们排成一条曲线，毕竟有猴子还在写《哈姆雷特》。可以一逝</p>
                <p>Ken Perlin发明的柏林噪声算法为我们提供了一种方法，通过从固定数据中伪随机，相同的数据通过函数可以得到相同的结果，但不同的数据即使差距微乎其微也可能导致巨大的结果差异，且似乎没有规律可循，从而有一种随机的假象</p>
                <p>再使用插值的思想，百度：插值是离散函数逼近的重要方法，利用它可通过函数在有限个点处的取值状况，估算出函数在其他点处的近似值，从而在混乱中生成一定的秩序</p>
                
                <h2>一维噪声</h2>
                <p>其实一维噪声并不是像上方举例的一样总是对于线而言，很多参数例如颜色、高度都可以使用该算法，但是为了简单起见还是先从线入手</p>
                <p>不论是哪一个维度的噪声都需要先确定一个值（其实我怀疑一维并不需要，因为总能计算为单位长度），一维为线段长度（下面称为L），二维为正方形边长，三维为正方体体长</p>
                <p>假如令L = 5，则在直角坐标系中的x轴上每5个单位取一个纵坐标为0的点，并为每个点定一个伪随机值表示其切线斜率</p>
                <p>然后取任意两个相邻点进行分析（下图中为A、B），假定A伪随机值为 m,B为 n。C为AB间任意一点，D为曲线上点且横坐标与C相同</p>
                <div>
                    <img src="../../assets/function.png" id="fun_intro">
                </div>
                <p>由于AB = L = 5，则AC取值范围为 [0,5]，设X = AC / 5，即将AC范围值缩小至一个单位长度上，同理设Y = BC / 5。再令FA = mX，FB = nY，可见FA、FB为斜率与横坐标乘积，可表示纵坐标（高度）</p>
                <p>此时我们再通过插值即可得到任一点处高度，即构造一个函数使其穿过AB两点，该方法不唯一，在柏林噪声算法中使用的是fn = FA + t ( FA - FB ) = CD。起初t就是用X的值计算的，但这会导致将fn展开后二次求导会得到一个常数，说明斜率的变化率固定使曲线十分不平滑；后来提出令t = 3X^2 - 2X^3，展开fn算一下二阶导会发现有一个常数项，那么这个也会导致曲线的人工生成痕迹（好像对一阶的影响不大，但是二阶及以上就会较为明显）；最终改进版是选取了t = 6X^5 - 15X^4 + 10X^3，最低阶都是三阶了从而二阶导没有了常数项</p>
                <p>选取这个函数还有如下原因：</p>
                <ul id="ululul">
                    <li>当X = 0时，t = 0，当X = 1时，t = 1,从而在上述两个情况时都有fn = 0，刚好对应了AB两点</li>
                    <li>当定义域为[0,1]时值域也为[0,1]，且在0到1上的函数图像要关于（0.5,0.5）中心对称，我想这大概可能或许是为了保证图像在一定范围内的相似性？</li>
                    <li>在[0,1]定义域内函数变化在两端慢，中间快，且在(0,0)(1,1)导数为0，大概是为了让图像在连接点AB处平滑，也与上述提到的二阶导有关</li>
                </ul>
                <div id="ming">
                    <img src="../../assets/list1/save (3).png">
                </div>
                <p>当然还有比如1/2 - cos(πx)/2的三角函数也是可行的，但是由于以上第三个函数最符合第三条原因，所以还是选用它最好</p>
                <p>至此，算法部分已经解决了，问题在于伪随机值应该如何取得，我看网上的一些文章是以甚至称不上随机的方法取的，后来看别人说才知道他们实现的其实是一种叫value的噪声，它的特点是直接使用随机值计算，但是这种方法还是对一维噪声比较友好的，运行效率较高也不会使噪声十分混乱，但是二阶及以上就不太行了。下面先使用value噪声进行演示</p>
                <h2>一维噪声——值噪声</h2>
                <div id="bid">
                <div style="display:flex;justify-content:center;overflow: hidden;">
                    <canvas id="canvas6" width="800" height="400"></canvas>
                </div>
                <div id="btns_cir">
                    <div class="btn_cir">
                        <input_scroll label="setting_ly" setting_state="false" :stroage="false" v-model="sbstate" style="margin:0 20px"></input_scroll>
                        <span>是否填充</span>
                    </div>
                    <div class="btn_cir">
                        <div><num-input :max="200" :min="1" v-model="strange_m" :step="9"></num-input></div>
                        <span>平滑段数:    数值越低形状越圆润，反之越尖锐</span>
                    </div>
                    <div class="btn_cir">
                        <div><num-input :max="4" :min="Math.PI/210" v-model="strange_h" :step="Math.PI/20"></num-input></div>
                        <span>尖角边数:    数值越高则形状的多边形性状越明显，平滑段数基于尖角边数</span>
                    </div>
                    <div class="btn_cir">
                        <div><num-input :max="20" :min="1" v-model="strange_n" :step="1"></num-input></div>
                        <span>差异度: 数值越低图形在一定区域内相似度越高</span>
                    </div>
                    <div class="btn_cir">
                        <div><num-input :max="20" :min="0" v-model="strange_t" :step="1"></num-input></div>
                        <span>附加幅度: 数值越高波动幅度越大</span>
                    </div>
                    <div class="btn_cir">
                        <div><num-input :max="20" :min="1" v-model="strange_p" :step="1"></num-input></div>
                        <span>速度及幅度: 数值高则波动速度快但幅度将下降</span>
                    </div>
                </div>
                </div>
                <p>以上就是一维噪声的应用，首先是要画出一圈圆，至于用不用噪声都无所谓，完成后的还只是一个静态形状，只需要用噪声算法赋予圆上各点不同的波动幅度即可</p>
                <p>第一步对圆上的360度分段，为每段的连接点设置随机值/斜率</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            let array = [];
            var anglegap = this.strange_m;
            var anpace = 360 / anglegap;
            for(let n = 0;n&lt;anglegap;n++){
                array.push(Math.random()*40-20);
            }
                    </code></pre>
    
                </div>

                <p>为了使用方便，还封装了一个插值函数。共有三个参数，x表示横坐标，pace表示设置的相邻两个取斜率的点距离，array存放的就是取得的斜率，最终返回一个高度值（或者有其它含义）。"|0"就是向下取整，去掉小数部分，从而计算出与任一点相距最近的两个相邻斜率点。由于要画出一个封闭的形状，所以第一个和最后一个点的值肯定要是一样的，因此计算yy时超出数组长度直接返回第一个值</p>
                <div class="tons_code">
                    <pre v-highlight><code>
        function noise(x,pace,array){
            let ratio = x / pace;
            let z = ratio | 0;
            let dx = ratio - z;
            let dy = dx - 1;
            let t = 6*Math.pow(dx,5)-15*Math.pow(dx,4)+10*Math.pow(dx,3);
            let xx = dx*array[z];
            let yy = dy*(array[z+1] != undefined ? array[z+1] : array[0]);
            return xx + t*(yy-xx)
        }
                    </code></pre>
                </div>
                

                <p>然后再写一下画图的函数，for循环一下隔一定弧度取一个要计算初始波动幅值的点，但是我发现其实for是不会刚好取到2π这个值的，总会有一点差距，所以最后还得自己成一圈，虽然可以通过减小每次for循环增量解决，但是可能会让电脑炸了。计算点位置的方式又让我想起了大学物理。把点的位置信息存入数组ecc，最后通过遍历全部画出来。函数的最后会判断一下用什么方式画图，填充或者连线</p>
                <div class="tons_code">
                    <pre v-highlight><code>
        function draw(){
            window.requestAnimationFrame(draw);
            context6.clearRect(0, 0, canvas6.width, canvas6.height);
            let ecc = [];
            for(let an = 0;an&lt; = Math.PI*2;an += that.strange_h){  //尖角边数
                let length = 100+noise(an*180/Math.PI,anpace,array)
                ecc.push({x:length*Math.cos(an),y:length*Math.sin(an)})
            }
            ecc.push({x:100,y:0})
            context6.beginPath()
            ecc.forEach((item,index) = &gt;{
                if(index === 0){
                    context6.moveTo(400 + item.x,200 + item.y)
                }else{
                    context6.lineTo(400 + item.x,200 + item.y)
                }
            })
            if(!that.sbstate){   //是否填充
                context6.strokeStyle = '#5bd4b0';
                context6.stroke()
            }else{
                context6.fillStyle = '#5bd4b0';
                context6.fill()
            }
        }
                    </code></pre>
                </div>



                <p>这样已经可以画出静态的形状了，在上面展示中把附加幅值调成0就能看出来。之前我就想过这个算法的本质是什么呢？它实质上是为我们创造出了一段连续平滑的函数，这个就可以很多事了，比如为每一个点添加一个连续平滑的动画。先把函数的参数确定了</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            let afc = []
            var dll = this.strange_n  //差异度/斜率点距离
            var sll = 360/dll   //360为函数定义域
            for(let n = 0;n&lt;dll;n++){
                afc.push(Math.random()*this.strange_t-this.strange_t / 2) //附加幅值
            }
                    </code></pre>
                </div>


                <p>把上面的数据传入构造好的noise()函数即可获得一个新函数，我们取一个点从原点开始沿x轴向右运动就得到了函数上的连续流畅变化的值，遍历为第一步得到的各个斜率点的斜率值添加上该函数的变化，但是这里并不是简单的加，这样会导致图像每个部分的变化相同而没有什么差异，通过e+step*x(x为斜率点排在的位数)可以让添加的函数从不同的地方开始变化,step就为不同开始位置的间隔。我们不能忘了得到的函数是有定义域的，就算把它设到很大很大也总会超的，因此下面通过new_e把任一点放回到定义域的区间，可以理解为每隔定义域长度就为相同函数图像</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            let e = 0    //沿x轴移动的点
            let speed = this.strange_p / 10   //附加幅值及速度

            //...以下为draw()函数中内容
            e+=speed
            for(let x = 0;x&lt;anglegap;x++){
                let step = 360/anglegap
                let new_e = (e+step*x) % 360  
                array[x] += noise(new_e,sll,afc)*40  //幅度
            }
                    </code></pre>
                </div>

                <p>再说一下一个用了很多时间才找到的坑，其实一开始我得到的图形形状是下图这样的，这个奇怪的位置就是第一和倒一斜率点的交界，导致这个问题的原因在于两个点的通过函数附加的额外斜率不同，而初始斜率又相同，看回上面一段对new_e的代码我一开始是这样写的</p>
                <div style="display:flex;justify-content: center;">
                    <img src="../../assets/list1/151352(1).png" style="transform:scale(0.7)">
                </div>
                <div class="tons_code">
                    <pre v-highlight><code>
            let new_e = (e+2 * x) % 360
                    </code></pre>
                </div>
                <p>没错，一开始没有用step，每个斜率点附加的函数开始的地方的间隔统一设为了2，那么第一和倒一的点就有比如20的距离，而定义域却有360的距离，从这相隔20的不同点开始的函数取值是有很大差距的，所以必须保证第一倒一的距离接近定义域长度，用上上一段的step代码就可以解决了</p>
                <p>完整代码</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            const that=this 
            var canvas6=document.getElementById('canvas6')
            var context6=canvas6.getContext('2d');
            let array=[]
            var anglegap=this.strange_m   //个数
            var anpace=360/anglegap
            for(let n=0;n&lt;anglegap;n++){
                array.push(Math.random()*40-20)   //初始幅值
            }
            function noise(x,pace,array){
                let ratio = x/pace
                let z = ratio|0
                let dx = ratio-z
                let dy = dx - 1
                let t = 6*Math.pow(dx,5)-15*Math.pow(dx,4)+10*Math.pow(dx,3)
                let xx = dx*array[z]
                let yy = dy*(array[z+1] !=undefined ? array[z+1] : array[0])
                return xx+t*(yy - xx)
            }

            let afc = []
            var dll = this.strange_n  //差异度
            var sll = 360/dll
            for(let n = 0;n&lt;dll;n++){
                afc.push(Math.random()*this.strange_t-this.strange_t/2) //附加幅值
            }

            let e=0
            var speed=this.strange_p/10 //附加幅值及速度

            function draw(){
                window.requestAnimationFrame(draw)
                context6.clearRect(0, 0, canvas6.width, canvas6.height);
                let ecc=[]
                for(let an=0;an&lt;=Math.PI*2;an+=that.strange_h){  //平滑边数
                    let length = 100+noise(an*180/Math.PI,anpace,array)
                    ecc.push({x:length*Math.cos(an),y:length*Math.sin(an)})
                }
                ecc.push({x:100,y:0})
                context6.beginPath()
                ecc.forEach((item,index)=&gt;{
                    if(index===0){
                        context6.moveTo(400+item.x,200+item.y)
                    }else{
                        context6.lineTo(400+item.x,200+item.y)
                    }
                })
                if(!that.sbstate){
                    context6.strokeStyle='#5bd4b0';
                    context6.stroke()
                }else{
                    context6.fillStyle='#5bd4b0';
                    context6.fill()
                }

                e+=speed
                for(let x=0;x&lt;anglegap;x++){
                    let step = 360/anglegap
                    let new_e = (e+step*x) % 360  
                    array[x] += noise(new_e,sll,afc)*40  //附加幅值
                }
            }
            window.requestAnimationFrame(draw)
                    </code></pre>
                </div>
                <h2>一维噪声——伪随机数生成器(PRNG)</h2>
                <p>下面进入真正的一维柏林噪声，文章来自网站：<a target="_blank" href="https://oliverbalfour.github.io/javascript/2016/03/19/1d-perlin-noise.html">https://oliverbalfour.github.io/javascript/2016/03/19/1d-perlin-noise.html</a>的内容</p>
                <p>因为看不懂...所以直接复制一下代码和原文翻译:"</p>
                <p>您可能会问为什么我们使用 PRNG （伪随机数生成器）而不是 Math.random()（随机数生成器）。这仅仅是因为您可以保存seed种子并获得相同的结果,很多游戏都依赖于此。以我的世界为例,当你生成一个世界时，你可以选择输入一个种子作为世界的基础,相同的种子总是产生相同的地图。而说到那个世界的生成，你能猜到地形是怎么生成的吗？多维柏林噪声,你明白了</p>
                <p>现在，我们将实际制作一个 PRNG。我们将制作一个线性同余生成器，简称 LCG。这是一个特别简单的 PRNG，只需要几行代码就能看到它的神奇之处</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            const M = 4294967296 // a - 1应可被 m 的质因数整除
            const A = 1664525 // c and m 应互质
            const C = 1
            var seed = Math.floor(Math.random() * M); //可以得到(0,M)之间的随机整数

            function rand(){
	            seed = (A * seed + C) % M;
	            return seed / M;
            }
                    </code></pre>
                </div>
                <p>不要担心那里的巨大数字，它们是我们 LCG 的参数。我选择大数字是因为你得到的数字序列的长度等于M. 所以M越大，在出现重复之前你可以得到的可能值就越多。<a style="color:var(--blue)" href="https://en.wikipedia.org/wiki/Linear_congruential_generator#Parameters_in_common_use" target="_blank">这是参数组合的列表</a>"</p>
                <p>虽然看完一脸懵13，但是丝毫不影响我们直接使用它生成伪随机数(0.0)，在二维中还会讲到柏林噪声发明者使用的伪随机数生成器</p>
                <h2>二维噪声</h2>
                <div class="resizebox">
                    <div id="cont">
                        <canvas id="canvas8" width="400" height="400"></canvas>
                    </div>
                </div>
                <p>对于二维噪声，我们需要先在平面坐标系上确定正方形的边长，为了方便可以直接取值为1，与一维取斜率不同的是，二维需要在每个正方形的顶点取一个伪随机向量（下图一）。此时再对任一正方形（下图二）进行分析</p>
                <div id="vreaq">
                    <img src="../../assets/list1/vrvre.png">
                    <img src="../../assets/list1/2dnoise.png">
                </div>
                <p>正方形上一点可取得4个从顶点指向该顶点的向量（含零向量），分别将四个向量点乘顶点对应伪随机向量，下图所示</p>
                <div class="resizebox">
                    <img class="imgcontain" src="../../assets/list1/MommyTalk1659523149296.png">
                </div>
                <p>对所得值进行插值处理，跟一维的方式一样，不过要做如下三次</p>
                <div class="resizebox">
                    <img style="max-width:50%" class="imgcontain" src="../../assets/list1/MommyTalk1659530502678.png">
                </div>
                <p>最后得到值Z，若视为高度则可认为生成了一个平滑连续的曲面，但是正方形顶点处Z值不像一维为0</p>


                <h2>二维噪声——哈希函数</h2>
                <p>以下内容参考了关于三维柏林噪声的文章：<a target="_blank" href="http://adrianb.io/2014/08/09/perlinnoise.html">http://adrianb.io/2014/08/09/perlinnoise.html</a></p>
                <p>又到了取伪随机值的时候，首先需要说明二维能取到的值（向量）最好是有限固定的，比如下图是我选取的向量</p>
                
                
                <div class="resizebox">
                    <img style="max-width:40%" class="imgcontain" src="../../assets/list1/MommyTalk1659533160334.png">
                </div>

                <p>因为呢据网友解说：研究噪声时发现向量均匀分布时得到的效果更好，像上图向量相邻之间夹角均为45°，刚好绕成一圈，也可以让夹角为22.5°取16个向量；并且向量中只有-1，0，1可以有效简化向量点乘部分代码提高效率</p>

                <p>要用伪随机从中取值就要介绍柏林噪声使用的哈希算法，这种算法不是唯一的，就是要尽可能避免输入值不同而输出相同（哈希冲突），当然在柏林噪声中并不需要太考虑这一点，还要使输入相同数据时输出相同</p>
