                <p>对于任意点，获得其坐标便知道了其对应在哪个正方形中。对于点（x，y），先对x、y按位运算，得floorx、floory（当然，这会导致一个问题，每255个单位必然会导致取值的重复，此时可以对x除数缩小处理，或者直接扩大数组长度），可知正方形顶点坐标如下：</p>
                <div class="resizebox">
                    <img class="imgcontain" style="max-width:50%" src="../../assets/list1/MommyTalk1659592378240.png">
                </div>
                <p>将坐标分别进行下方索引（哈希函数）可为每一点确定一值nx，用于求取向量</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            let floorx = x & 255
            let floory = y & 255
                    
            let n1 = table[floorx + table[floory]]
            let n2 = table[floorx + 1 + table[floory]]
            let n3 = table[floorx + table[floory + 1]]
            let n4 = table[floorx + 1 + table[floory + 1]]
                    </code></pre>
                </div>

                <p>nx按位运算&7后最多得8个值，分别从8个固定向量中对应一个不同向量点乘运算即可，从而实现了我也不知道你会取到哪个向量的伪随机效果</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            function getgradients(hash,x,y){
                switch(hash & 7){
                    case 0: return y;
                    case 1: return x + y;
                    case 2: return x;
                    case 3: return x - y;
                    case 4: return - y;
                    case 5: return - x - y;
                    case 6: return - x;
                    case 7: return - x + y;}
            }      
                    </code></pre>
                </div>

                <p>完整代码</p>
                <div class="tons_code">
                    <pre v-highlight><code>    
            function iplt(dx,x,y){      //插值函数
                let t=6*Math.pow(dx,5)-15*Math.pow(dx,4)+10*Math.pow(dx,3)
                return x+t*(y-x)
            }
            function getgradients(hash,x,y){        //取向量
                switch(hash & 7){
                    case 0: return y
                    case 1: return x + y
                    case 2: return x
                    case 3: return x - y
                    case 4: return - y
                    case 5: return - x - y
                    case 6: return - x
                    case 7: return - x + y
                }   
            }
            function tdnoise(x,y){      //噪声函数
                let bx = x - (x | 0)
                let by = y - (y | 0)
                    
                let floorx = x & 255
                let floory = y & 255
                    
                let n1 = table[floorx + table[floory]]
                let n2 = table[floorx + 1 + table[floory]]
                let n3 = table[floorx + table[floory + 1]]
                let n4 = table[floorx + 1 + table[floory + 1]]

                let m1 = getgradients(n1,bx,by)
                let m2 = getgradients(n2,bx-1,by)
                let m3 = getgradients(n3,bx,by-1)
                let m4 = getgradients(n4,bx-1,by-1)

                let r1 = iplt(bx,m1,m2)
                let r2 = iplt(bx,m3,m4)

                return iplt(by,r1,r2)
            }
                    </code></pre>
                </div>

                <h2>二维噪声——用法</h2>
                <p>二维噪声一种简单的理解就是为平面上的点赋值。比如对下图区域的值取绝对值，并按值的大小取像素的透明度</p>
                <div class="resizebox">
                    <canvas id="canvas7" width="300" height="300"></canvas>
                </div>
                <p>一开始我还想着是不是用打点实现，试了一下浏览器直接卡炸了，之后才知道原来要直接对像素进行处理</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            var image = context7.createImageData(canvas7.width, canvas7.height); 
            var data = image.data;

            for(let x = 0;x&lt;canvas7.width;x+=1){
                for(let y = 0;y&lt;canvas7.height;y+=1){
                    var c = (x + y * canvas7.width) * 4;
                    data[c] = 91 
                    data[c + 1] = 212
                    data[c + 2] = 176
                    data[c + 3] = Math.max(0,Math.abs(tdnoise(x/100,y/100)*300))
                }
            }
            
            context7.putImageData(image, 0, 0)
                    </code></pre>
                </div>

                <p>感觉这个得到的图还是挺丑的，毕竟返回值为0点几的点连成的线都是黑色的，所以可以换一个想法：对于排列整齐的直线，我们可以用返回值表示线上点的横坐标偏移量，从而把它们转成曲线。于是就有了刚开始写二维曲线那里的那张图,本来是做了动态效果，但是因为所谓的曲线是一段段直线构成的，细分大了动态效果不好，细分小了又有性能问题，最后干脆删掉了</p>

                <div class="tons_code">
                    <pre v-highlight><code>
            const canvas8=document.getElementById('canvas8')
            const context8=canvas8.getContext('2d');
            context8.clearRect(0,0,canvas8.width,canvas8.height)
