                context8.moveTo(x,-10)
                for(let y = 0;y&lt;canvas8.height+1;y+=5){
                    context8.lineTo(x+jnj[x/5*(canvas8.width/5+1)+y/5],y)
                    fec.addColorStop(y/canvas8.height, "rgb(91, 212, 176,"+Math.max(0.2,Math.abs(jnj[x/5*(canvas8.width/5+1)+y/5]/30))+")");     
                }
                    context8.strokeStyle = fec
                    context8.stroke()
                    </code></pre>
