<template>
    <div id="snoise">
        <div id="snoise_body">
            
            <canvas id="snoise"></canvas>
        </div>
    </div>
</template>

<script>
    export default {
    name: 'snoise',
        data() {
            return {
                resizelistener: 0,
                observer: null,
                add:null
            }
    },
    watch: {

    },
    mounted() {
        var that = this
        var snoise = document.getElementById('snoise')
        var snoisectx = snoise.getContext('2d')
        var snoise_body = document.getElementById('snoise_body')

        snoise.width = snoise_body.clientWidth
        snoise.height = snoise_body.clientHeight



        this.resizelistener = () => {
            // document.getElementById('left').addEventListener('transitionend', () => {
                

                    snoise.width = snoise_body.clientWidth
                    snoise.height = snoise_body.clientHeight

                // })

        }

        window.addEventListener('resize', this.resizelistener,false)

        //单纯形噪声定义
        var hashtable = [151, 160, 137, 91, 90, 15, 131, 13, 201, 95, 96, 53, 194, 233, 7, 225, 140, 36, 103, 30, 69, 142, 8, 99, 37, 240, 21, 10, 23, 190, 6, 148, 247, 120, 234, 75, 0, 26, 197, 62, 94, 252, 219, 203, 117, 35, 11, 32, 57, 177, 33,
            88, 237, 149, 56, 87, 174, 20, 125, 136, 171, 168, 68, 175, 74, 165, 71, 134, 139, 48, 27, 166, 77, 146, 158, 231, 83, 111, 229, 122, 60, 211, 133, 230, 220, 105, 92, 41, 55, 46, 245, 40, 244, 102, 143, 54, 65, 25, 63, 161, 1, 216, 80, 73, 209, 76, 132, 187, 208, 89, 18, 169, 200, 196, 135, 130, 116, 188, 159, 86, 164, 100, 109, 198, 173, 186, 3, 64, 52, 217, 226, 250, 124, 123,
            5, 202, 38, 147, 118, 126, 255, 82, 85, 212, 207, 206, 59, 227, 47, 16, 58, 17, 182, 189, 28, 42, 223, 183, 170, 213, 119, 248, 152, 2, 44, 154, 163, 70, 221, 153, 101, 155, 167, 43, 172, 9, 129, 22, 39, 253, 19, 98, 108, 110, 79, 113, 224, 232, 178, 185, 112, 104, 218, 246, 97, 228, 251, 34, 242, 193, 238, 210, 144, 12, 191, 179, 162, 241, 81, 51, 145, 235, 249, 14, 239, 107,
            49, 192, 214, 31, 181, 199, 106, 157, 184, 84, 204, 176, 115, 121, 50, 45, 127, 4, 150, 254, 138, 236, 205, 93, 222, 114, 67, 29, 24, 72, 243, 141, 128, 195, 78, 66, 215, 61, 156, 180,

            151, 160, 137, 91, 90, 15, 131, 13, 201, 95, 96, 53, 194, 233, 7, 225, 140, 36, 103, 30, 69, 142, 8, 99, 37, 240, 21, 10, 23, 190, 6, 148, 247, 120, 234, 75, 0, 26, 197, 62, 94, 252, 219, 203, 117, 35, 11, 32, 57, 177, 33,
            88, 237, 149, 56, 87, 174, 20, 125, 136, 171, 168, 68, 175, 74, 165, 71, 134, 139, 48, 27, 166, 77, 146, 158, 231, 83, 111, 229, 122, 60, 211, 133, 230, 220, 105, 92, 41, 55, 46, 245, 40, 244, 102, 143, 54, 65, 25, 63, 161, 1, 216, 80, 73, 209, 76, 132, 187, 208, 89, 18, 169, 200, 196, 135, 130, 116, 188, 159, 86, 164, 100, 109, 198, 173, 186, 3, 64, 52, 217, 226, 250, 124, 123,
            5, 202, 38, 147, 118, 126, 255, 82, 85, 212, 207, 206, 59, 227, 47, 16, 58, 17, 182, 189, 28, 42, 223, 183, 170, 213, 119, 248, 152, 2, 44, 154, 163, 70, 221, 153, 101, 155, 167, 43, 172, 9, 129, 22, 39, 253, 19, 98, 108, 110, 79, 113, 224, 232, 178, 185, 112, 104, 218, 246, 97, 228, 251, 34, 242, 193, 238, 210, 144, 12, 191, 179, 162, 241, 81, 51, 145, 235, 249, 14, 239, 107,
            49, 192, 214, 31, 181, 199, 106, 157, 184, 84, 204, 176, 115, 121, 50, 45, 127, 4, 150, 254, 138, 236, 205, 93, 222, 114, 67, 29, 24, 72, 243, 141, 128, 195, 78, 66, 215, 61, 156, 180]
        const F1 = 0.5 * (Math.sqrt(3) - 1)
        const F2 = (3 - Math.sqrt(3)) / 6
        function grad(f, x, y) {
            switch (f & 7) {
                case 0: return y
                case 1: return x + y
                case 2: return x
                case 3: return x - y
                case 4: return - y
                case 5: return - x - y
                case 6: return - x + y
                case 7: return - x
            }
        }
        function simplex(x, y) {
            var x_y = (x + y) * F1
            var sx = (x + x_y) | 0
            var sy = (y + x_y) | 0
            var sx_xy = (sx + sy) * F2
            var offsetx = x - (sx - sx_xy)
            var offsety = y - (sy - sx_xy)
            var hash1 = sx & 255
            var hash2 = sy & 255
            var zdwyl1 = hashtable[hash2]
            var zdwyl2 = hashtable[hash2 + 1]
            if (offsetx > offsety) {
                var x1 = offsetx - 1 + F2
                var y1 = offsety + F2
                var x2 = offsetx - 1 + 2 * F2
                var y2 = offsety - 1 + 2 * F2
                var f1 = hashtable[hash1 + zdwyl1]
                var f2 = hashtable[hash1 + 1 + zdwyl1]
                var f3 = hashtable[hash1 + 1 + zdwyl2]
            } else {
                var x1 = offsetx + F2
                var y1 = offsety - 1 + F2
                var x2 = offsetx - 1 + 2 * F2
                var y2 = offsety - 1 + 2 * F2
                var f1 = hashtable[hash1 + zdwyl1]
                var f2 = hashtable[hash1 + zdwyl2]
                var f3 = hashtable[hash1 + 1 + zdwyl2]
            }
            var o1 = 0.5 - offsetx * offsetx - offsety * offsety
            var o2 = 0.5 - x1 * x1 - y1 * y1
            var o3 = 0.5 - x2 * x2 - y2 * y2
            var len1 = (o1 >= 0 ? o1 : 0)
            var len2 = (o2 >= 0 ? o2 : 0)
            var len3 = (o3 >= 0 ? o3 : 0)
            var n1 = len1 * len1 * len1 * len1 * grad(f1, offsetx, offsety)
            var n2 = len2 * len2 * len2 * len2 * grad(f2, x1, y1)
            var n3 = len3 * len3 * len3 * len3 * grad(f3, x2, y2)
            return 70 * (n1 + n2 + n3)
        }

        var nn = 0
        function draw() {
            // var image = snoisectx.createImageData(snoise.width, snoise.height)
            // var data = image.data
            snoisectx.clearRect(0,0,snoise.width, snoise.height)
            that.add = requestAnimationFrame(draw)
            nn+=0.07
            for (let x = 0; x < snoise.width; x+=10) {
                for (let y = 0; y < snoise.height; y+=10) {
                    // let cell = (x + snoise.width * y) * 4

                    var aa = Math.abs(1.1 * simplex((x + 90) / 500, (y + 90) / 500)+10*simplex((x + 900) / 1000, (y + 900) / 1000)+2*simplex(x/120,y/120)+simplex(x/60,y/60))
                    aa = Math.floor(simplex(aa / 100, aa / 100) / 0.83*10)
                    snoisectx.fillText((aa + nn|0)%9, x, y,20)
                    snoisectx.font = '10px Roboto Mono'
                    snoisectx.textAlign = 'left'
                    // snoisectx.textBaseline='left'
                    snoisectx.fillStyle='white'
                    snoisectx.fill()
                        // data[cell] = aa * 255;
                        // data[cell + 1] =aa*255;
                        // data[cell + 2] =255;
                        // data[cell + 3] = 255;     
                }
            }

        }
        draw()

        

        var observer = new MutationObserver(() => {
            setTimeout(() => {
                snoise.width = snoise_body.clientWidth
                snoise.height = snoise_body.clientHeight
            }, 200)
        })

        observer.observe(document.getElementById('left'), { attributes: true })
            
    },
    beforeDestroy() {
        window.removeEventListener('resize', this.resizelistener)
        cancelAnimationFrame(this.add)

        }
    }
</script>

<style lang="" scoped>
    #snoise_body{
        height: 100vh;
        overflow: hidden;
    }
    
</style>


