<template>
    <div id="particular_b" style="display: none;">
        <div id="particular_b_body">
            <div id="particular_b_content">
                <canvas id="particular_b_canvas"></canvas>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'particular_b',
        data() {
            return {
                
            }
    },
    mounted() {
        const particular_b_canvas = document.getElementById('particular_b_canvas')
        const particular_b_ctx = particular_b_canvas.getContext('2d')
        particular_b_canvas.width = 500;
        particular_b_canvas.height = 500;


        const box_radius = 200;
        const box_xCenter = particular_b_canvas.clientWidth / 2;
        const box_yCenter = particular_b_canvas.clientHeight / 2;


        particular_b_canvas.lineJoin = "round";
        particular_b_canvas.lineCap = "round";


        particular_b_ctx.translate(0, particular_b_canvas.clientHeight);
        particular_b_ctx.scale(1, -1);




        
        
        
        


        class particular{
            constructor(x,y,v,angle,radius) {
                this.x = x;
                this.y = y;
                this.v = v;
                this.angle = angle;
                this.radius = radius;
                this.dx = this.v * Math.cos(this.angle);
                this.dy = this.v * Math.sin(this.angle);
                this.dotarray = [];



                this.inidot = () => {
                    for (let x = 0; x < 300; x++){
                        this.dotarray.push([this.x, this.y]);
                    }
                }
                this.inidot()

                this.draw = () => {
                    particular_b_ctx.beginPath()
                    particular_b_ctx.arc(this.x, this.y, this.radius, 0, Math.PI * 2)
                    particular_b_ctx.fillStyle = 'rgb(68, 115, 255)'
                    particular_b_ctx.fill();
                }
                this.bounce = () => {

                    const ddx = this.x + this.v * Math.cos(this.angle) - box_xCenter;
                    const ddy = this.y + this.v * Math.sin(this.angle) - box_yCenter;

                    
                    const disFromCenter = Math.sqrt(ddx * ddx + ddy * ddy);
                    if (disFromCenter >= (box_radius+1)) {

                        const supportVectorX = this.x + this.v * Math.cos(this.angle) - box_xCenter;
                        const supportVectorY = this.y + this.v * Math.sin(this.angle) - box_yCenter;
                        const cos0 = (this.dx * supportVectorX + this.dy * supportVectorY) / Math.sqrt(this.dx * this.dx + this.dy * this.dy) / Math.sqrt(supportVectorX * supportVectorX + supportVectorY * supportVectorY);
                        const _2rcos0 = 2 * disFromCenter * cos0;
                        const breakLength = (_2rcos0 - Math.sqrt(_2rcos0 * _2rcos0 - 4 * disFromCenter * disFromCenter + 4 * box_radius * box_radius)) / 2;

                        let breakPointX = this.x + this.v * Math.cos(this.angle) - breakLength * Math.cos(this.angle);
                        let breakPointY = this.y + this.v * Math.sin(this.angle) - breakLength * Math.sin(this.angle);
                        

                        

                        // const breakAngle = Math.atan(Math.abs(ddy / ddx));
                        // breakPointX = ddx / Math.abs(ddx) * box_radius * Math.cos(breakAngle) + box_xCenter;
                        // breakPointY = ddy / Math.abs(ddy) * box_radius * Math.sin(breakAngle) + box_yCenter;
                        particular_b_ctx.beginPath()
                        particular_b_ctx.arc(breakPointX, breakPointY, 8, 0, Math.PI * 2)
                        particular_b_ctx.fillStyle = 'red'
                        particular_b_ctx.fill();
                        let breakVectorX = 1;
                        let breakVectorY = breakVectorX * (box_xCenter - breakPointX) / (breakPointY - box_yCenter);
                        if (breakPointX <= box_xCenter - box_radius) {
                            breakVectorX = 0;
                            breakVectorY = breakVectorX;
                        }
                        const outVectorX = this.dx;
                        const outVectorY = this.dy;
                        let rotateAngle = (outVectorX * breakVectorX + outVectorY * breakVectorY) / Math.sqrt(outVectorX * outVectorX + outVectorY * outVectorY) / Math.sqrt(breakVectorX * breakVectorX + breakVectorY * breakVectorY);
                        rotateAngle = Math.acos(rotateAngle);


                        
                        

                        if (breakPointY >= box_yCenter) {
                            if (rotateAngle <= Math.PI / 2) {
                                this.angle -= rotateAngle * 2;
                            } else {
                                this.angle += rotateAngle * 2;
                            }
                        } else {
                            if (rotateAngle <= Math.PI / 2) {
                                this.angle -= rotateAngle * 2;
                            } else {
                                this.angle += rotateAngle * 2;
                            }
                        }

                    }
                }
                this.move = () => {
                    this.x += this.v * Math.cos(this.angle)// + Math.random() * 0.5 - 0.25;
                    this.y += this.v * Math.sin(this.angle)// + Math.random() * 0.5 - 0.25;
                    this.dotarray.pop();
                    this.dotarray = [[this.x, this.y], ...this.dotarray];
                }

                this.drawVector = () => {
                    particular_b_ctx.beginPath();
                    this.dotarray.forEach((item, index) => {
                        if (index % 5 == 0) {
                            particular_b_ctx.lineTo(item[0], item[1]);
                        }
                    })
                    // var j = particular_b_ctx.createLinearGradient(this.dotarray[0][0], this.dotarray[0][1], this.dotarray[this.dotarray.length - 1][0], this.dotarray[this.dotarray.length - 1][1])
                    // j.addColorStop(0, 'rgb(255, 255, 255, 1)');
                    // j.addColorStop(1, 'rgb(255, 255, 255, 0)');
                    // particular_b_ctx.strokeStyle = j;
                    particular_b_ctx.stroke();
                }
            }
        }

        let particulars = [];
        for (let x = 0; x < 10; x++){

            const createRadius = 3;

            
            let getRandomRad = Math.random() * 2 * Math.PI - Math.PI;
            let offset_x = (box_radius - 30) * Math.cos(getRandomRad);
            let particular_x = box_xCenter + offset_x;
            let offset_y = Math.sqrt(box_radius * box_radius - offset_x * offset_x);
            let particular_y = box_yCenter + Math.random() * 0.8 * offset_y - offset_y * 0.8;

            const getAnotherRandomRad = Math.random() * 2 * Math.PI - Math.PI;
            

            for (let i = 0; i < particulars.length; i++) {
                const item = particulars[i];
                let distance = Math.sqrt((particular_x - item.x) * (particular_x - item.x) + (particular_y - item.y) * (particular_y - item.y));
                if (distance <= createRadius + item.radius) {
                    getRandomRad = Math.random() * 2 * Math.PI - Math.PI;
                    offset_x = (box_radius - 30) * Math.cos(getRandomRad);
                    particular_x = box_xCenter + offset_x;
                    offset_y = Math.sqrt(box_radius * box_radius - offset_x * offset_x);
                    particular_y = box_yCenter + Math.random() * 0.8 * offset_y - offset_y * 0.8;
                    distance = Math.sqrt((particular_x - item.x) * (particular_x - item.x) + (particular_y - item.y) * (particular_y - item.y));
                    i = -1;
                }

            }

            
            particulars.push(new particular(particular_x, particular_y, 0.5,-Math.PI/2,createRadius))
        }
        

        function run() {
            window.requestAnimationFrame(run)
            particular_b_ctx.clearRect(0, 0, box_xCenter * 2, box_yCenter * 2)

            particular_b_ctx.beginPath()
            particular_b_ctx.arc(box_xCenter, box_yCenter, 200, 0, Math.PI * 2);
            particular_b_ctx.lineWidth = 3;
            particular_b_ctx.strokeStyle = 'whitesmoke';
            particular_b_ctx.stroke();

            
            for (let t of particulars) {
                t.draw()
                t.bounce()
                t.move()
                particular_b_ctx.lineWidth = 2;
                t.drawVector()
            }  
        }
        run()
        
    }
    }
</script>

<style scoped>
    #particular_b_body{
        height: 100vh;
    }
    #particular_b_content{
        height: 100%;
        display: flex;
        align-items: center;
    }
</style>


