<template>
    <div id="beziercurse">
        <div id="be">
            <div id="be_body">



                <!-- <div id="be_head"></div> -->
                <h1 id="h11">贝塞尔曲线</h1>
                <h2>一阶曲线</h2>
                <p>只有一个起点和一个终点，因此只能确定一条直线</p>
                <h2>二阶曲线</h2>
                <p>相较于一阶曲线，二阶曲线多了一个控制点。将起点A、控制点B、终点C依次相连，在AB、BC上分别取点D、E，使得AD/AB = BE/BC = t（0&lt;=t&lt;=1），再在DE上取一点，使其到D距离比上DE长度也为t，当t从0增大到1时该点经过路径即为贝塞尔曲线，点坐标可由（1-t）² A + 2t（1-t） B + t ² C 表示</p>
                <canvas id="canvas1" width="850" height="300"></canvas>
                <div id="bi"><span>t 值：</span><input type="range" value="0" id="bi-range" min="0" max="100"><span style="width:20px">{{rvalue}}</span></div>
                <p>代码部分考虑到曲线已经确定，所以将曲线每0.001个单位分段，再根据t值判断取多少段直线，并计算端点坐标，由于直线长度很短所以最后连接看起来像曲线。端点坐标是以对象存入数组后再通过forEach遍历连接的，其实还可以每计算一个点连接一个，但是由于使用代码画圆十分奇葩，指必须先移动画笔位置画完圆弧再填充，而且要打一堆代码，因此在这种情况下位置的改变是没办法正常画曲线的</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            const canvas1=document.getElementById('canvas1');
            const context1=canvas1.getContext('2d');
            var plus=0;
            const range_this=this;
            const range=document.getElementById('bi-range');
            let aaa=[];
            range.oninput=function(){    //监听t值
                range_this.rvalue=range.value/100;
                plus=0;
                context1.clearRect(0, 0, canvas1.width, canvas1.height);
                aaa.length=0;
                for(let xx=0;xx&lt;10*range.value;xx++){    //取点
                    plus+=0.001;
                    aaa.push({x:100+800*plus-100*plus*plus,y:200-300*plus+370*plus*plus});
                }                                           
            }
            setInterval(function(){
                requestAnimationFrame(()=>{
                    var t=range.value/100;
                    context1.clearRect(0, 0, canvas1.width, canvas1.height);
                    context1.fillStyle='#A2D5F2';
                    context1.beginPath();
                    context1.strokeStyle='#5bd4b0'
                    aaa.forEach((item,index)=>{    //画线
                        if(index==0){
                            context1.moveTo(item.x,item.y);
                        }else{
                            context1.lineTo(item.x,item.y);
                        }
                    })
                context1.stroke()
                context1.beginPath()
                context1.moveTo(100, 200);
                context1.lineTo(500,50)
                context1.lineTo(800,270)
                context1.strokeStyle='#07689F'
                context1.lineWidth=6
                context1.stroke()
                context1.beginPath()    //无语的画圆
                context1.arc(100,200,8,0,2*Math.PI)
                context1.fill()
                context1.beginPath()
                context1.arc(500,50,8,0,2*Math.PI)
                context1.fill()
                context1.beginPath()
                context1.arc(800,270,8,0,2*Math.PI)
                context1.fill()
                context1.font='16px Arial'
                context1.fillText('A',100,180)
                context1.fillText('B',500,30)
                context1.fillText('C',800,250)
                context1.strokeStyle='#FF7E67'
                context1.lineWidth=4
                context1.beginPath()
                context1.fillText('D',80+400*t,180-150*t)
                context1.fillText('E',490+300*t,30+220*t)
                context1.moveTo(100+400*t,200-150*t)
                context1.lineTo(500+300*t,50+220*t)
                context1.stroke()  
                context1.beginPath()
                context1.fillStyle='#f2ac20'
                context1.arc(100+400*t,200-150*t,6,0,2*Math.PI)
                context1.fill()
                context1.beginPath()
                context1.arc(500+300*t,50+220*t,6,0,2*Math.PI)
                context1.fill()
                context1.beginPath()
                context1.fillStyle='#17B978'
                context1.arc(100+800*t-100*t*t,200-300*t+370*t*t,4,0,2*Math.PI)
                context1.fill()
                })
            })
                    </code></pre>
                </div>
                <h2>三阶曲线</h2>
                <p>三阶贝塞尔曲线的两个控制点可以用于描绘更复杂的曲线，连接四个初始点可得三条蓝色直线，在其上相同比例地取三个黄色点并相连，又得两条粉色线，线上取之前相同比例得二绿色点再连接，在最终的直线上按比例求得点即为描绘贝塞尔曲线的点。由于头发快掉完了，所以这个就不做交互了，可以在下面的高阶部分展示</p>
                <div>
                    <img src="../../assets/list1/vrev.png">
                </div>

                <h2>高阶曲线</h2>
                <p>下面是一个画板，可以展示高阶曲线的描绘，点击画板可以进行取点。为了画得好看一点，设置了渐变量，阶数越高越明显</p>
                <div id="can_con">
                    <div id="con_con">
                        <div>
                            <button id="draw_button">绘制</button>
                            <button id="redraw_button">重绘</button>
                            <button id="clear_button">清除</button>
                        </div>
                        <div>
                            <span>速度</span>
                            <button id="slowspeed_button" class="speed_active">0</button>
                            <button id="midspeed_button" class="speed_active" style="backgroundColor:#98C7E2;color:var(--gray)">龟速</button>
                            <button id="highspeed_button" class="speed_active">正常</button>
                        </div>
                        
                    </div>
                    <canvas id="canvas3" width="850" height="650" style="position:relative"></canvas>
                </div>
                <p>以上是采用递归的方法实现的，当然也可以采取更简单的方法 —— 根据方程一个一个点算</p>
                <div class="tons_code">
                    <pre v-highlight><code>
            const canvas3 = document.getElementById("canvas3");
            const context3 = canvas3.getContext("2d");
            const draw_button=document.getElementById('draw_button');
            const redraw_button=document.getElementById('redraw_button');
            const clear_button=document.getElementById('clear_button');
            let points = [];    //起点终点及控制点
            let final = [];     //曲线上的点
            let t = 0;          //t
            let Interval;       //定时器
            let drawstate=true; //描绘状态
            let redraw=true;    //重绘状态
            var speed=0.0001;   //速度
            let opacity=0 ;     //渐变量/点数


            function drawline() {       //画曲线
                context3.strokeStyle = "#5bd4b0";
                context3.lineWidth = 7;
                context3.beginPath();
                final.forEach((item, index) => {
                    if (index === 0) {
                        context3.moveTo(item.x, item.y);
                    } else {
                        context3.lineTo(item.x, item.y);
                    }
                });
                context3.stroke();
            }

            function curvepoint(point1, point2) {       //计算动点位置
                return{x:(point2.x-point1.x)*t+point1.x,y:(point2.y-point1.y)*t+point1.y};
            }

            function drawlines(points,color) {      //画直线
                if (points.length &lt; 2) {
                    const { x, y } = points[0];
                    final.push({ x, y });
                    drawline();
                    return;
                }
                context3.lineWidth = 2;
                context3.beginPath();
                for (let i = 0; i &lt; points.length; i++) {
                    const { x, y } = points[i];
                    if (i === 0) {
                        context3.moveTo(x, y);
                    } else {
                        context3.lineTo(x, y);
                    }
                }
                context3.strokeStyle= 'rgb(8, 103, 156,'+color+')';
                context3.stroke();
                const fpoints = [];
                for (let i = 0; i &lt; points.length - 1; i++) {
                    fpoints.push(curvepoint(points[i], points[i + 1]));
                }
                if(opacity>=5){
                    drawlines(fpoints,color-0.95/(opacity-1));
                }else{
                    drawlines(fpoints,color-0.2/(opacity-1));
                }
            
            }

            function draw(){        //画线
                if(drawstate){
                    if (points.length &lt; 2) {
                        alert('多画几个点吧');
                        drawstate=true
                        canvas3.addEventListener('click',doo)
                        return
                    }
                    drawlines(points,1);

                    const ani=()=>{
                        Interval=window.requestAnimationFrame(ani)
                        offset += speed
                        context3.clearRect(0, 0, canvas3.width, canvas3.height);
                        drawlines(points,1)
                        
                        if (offset >= 1) {
                            cancelAnimationFrame(Interval);
                            redraw=false
                        }
                    }
                    window.requestAnimationFrame(ani)
                    drawstate=false
                }
            }
            
            function doo(e){       //点击画布取点
                points.push({x:e.offsetX,y:e.offsetY});
                context3.beginPath();
                context3.arc(e.offsetX,e.offsetY,2,0,2*Math.PI);
                context3.fillStyle='whitesmoke';
                context3.fill();
                opacity++;
            }

            canvas3.addEventListener('click',doo)

            clear_button.addEventListener('click',()=>{     //清除
                context3.clearRect(0, 0, canvas3.width, canvas3.height)
                points = []
                final = []
                t = 0
                cancelAnimationFrame(Interval)
                drawstate=true
                opacity=0
                canvas3.addEventListener('click',doo)
            })

            draw_button.addEventListener('click',function(){        //开画
                canvas3.removeEventListener('click',doo);
                draw();
            })

            redraw_button.addEventListener("click", function () {       //重画
                if(!redraw){
                    final.length=0;
                    t = 0;
                    context3.clearRect(0, 0, canvas3.width, canvas3.height)
                    drawstate=true
                    redraw=true
                    draw();
                }
            });
                    </code></pre>
                </div>
                
                
            </div>
        </div>
    </div>
</template>

<script>


    export default {
        name: 'beziercurse',
        data() {
            return {
                rvalue:0,
            }
        },
        methods:{
            
        },
        mounted(){
            var canvas1=document.getElementById('canvas1')
            var context1=canvas1.getContext('2d')
            

            var plus=0
            var range_this=this                                            //此处的range_this指向的是父函数mounted()
            var range=document.getElementById('bi-range')
            
            let aaa=[]
            range.oninput=function(){
                range_this.rvalue=range.value/100                                       //如果这里直接用this会指向function()
                plus=0
                context1.clearRect(0, 0, canvas1.width, canvas1.height);
                aaa.length=0
                for(let xx=0;xx<10*range.value;xx++){
                        plus+=0.001
                        aaa.push({x:100+800*plus-100*plus*plus,y:200-300*plus+370*plus*plus})
                }                                           
            }


            
            
            

                
                setInterval(function(){
                    
                    
                    
                    
                requestAnimationFrame(()=>{
                var t=range.value/100
                context1.clearRect(0, 0, canvas1.width, canvas1.height);
                context1.fillStyle='#A2D5F2';

            
            context1.beginPath()
            context1.strokeStyle='#5bd4b0'
            aaa.forEach((item,index)=>{
                    if(index==0){
                        context1.moveTo(item.x,item.y)
                    }else{
                        context1.lineTo(item.x,item.y)
                    }
            })
            context1.stroke()
                    

            context1.beginPath()
            context1.moveTo(100, 200);
            context1.lineTo(500,50)
            context1.lineTo(800,270)
            context1.strokeStyle='#07689F'
            context1.lineWidth=6
            context1.stroke()


            context1.beginPath()
            context1.arc(100,200,8,0,2*Math.PI)
            context1.fill()
 

            context1.beginPath()
            context1.arc(500,50,8,0,2*Math.PI)
            context1.fill()
          

            context1.beginPath()
            context1.arc(800,270,8,0,2*Math.PI)
            context1.fill()

            
            context1.font='16px Arial'
            context1.fillText('A',100,180)
            context1.fillText('B',500,30)
            context1.fillText('C',800,250)


            context1.strokeStyle='#FF7E67'
            context1.lineWidth=4
            context1.beginPath()
            context1.fillText('D',80+400*t,180-150*t)
            context1.fillText('E',490+300*t,30+220*t)
            context1.moveTo(100+400*t,200-150*t)
            context1.lineTo(500+300*t,50+220*t)
            context1.stroke()


            
            context1.beginPath()
            context1.fillStyle='#f2ac20'
            context1.arc(100+400*t,200-150*t,6,0,2*Math.PI)
            context1.fill()

            context1.beginPath()
            context1.arc(500+300*t,50+220*t,6,0,2*Math.PI)
            context1.fill()


            context1.beginPath()
            context1.fillStyle='#17B978'
            context1.arc(100+800*t-100*t*t,200-300*t+370*t*t,4,0,2*Math.PI)
            context1.fill()


            


            
                })

            
                
                })
        
            
            
            
            
            
            
            // const code_block=document.querySelectorAll('.tons_code')
            // for(let p=0;p<code_block.length;p++){
            //     let ul=document.createElement('ul')
            //     ul.style.height='100%'
            //     ul.style.width='40px'
            //     ul.style.display='flex'
            //     ul.style.flexDirection='column'
            //     ul.style.justifyContent='center'
            //     ul.style.alignItems='center'
            //     ul.style.minWidth='40px'
            //     ul.style.fontSize='13px'
            //     ul.style.backgroundColor='#282C34'
            //     // ul.style.position='absolute'
            //     ul.style.fontFamily="'Roboto Mono', monospace"
            //     ul.style.listStyle='none'
            //     ul.style.userSelect='none'
            //     ul.style.borderRight='1px solid whitesmoke'
            //     let mm=$('.hljs')[p].innerHTML.split('\n').length
            //     for(let b=0;b<2;b++){
            //         let lines=document.createElement('li')
            //         let num=document.createTextNode('-')
            //         lines.appendChild(num)
            //         ul.appendChild(lines)
            //     }
            //     for(let n=1;n<=mm-2;n++){
            //         let lines=document.createElement('li')
            //         let num=document.createTextNode(n)
            //         lines.appendChild(num)
            //         ul.appendChild(lines)
            //     }
            //     code_block[p].insertBefore(ul,code_block[p].childNodes[0])
            // }

            
            const canvas3 = document.getElementById("canvas3");
            const context3 = canvas3.getContext("2d");
            const draw_button=document.getElementById('draw_button')
            const redraw_button=document.getElementById('redraw_button')
            const clear_button=document.getElementById('clear_button')
            let points = [];
            let final = [];
            let offset = 0;
            let Interval;
            let drawstate=true;
            let redraw=true;
            var speed=0.0001;
            let opacity=0 ;

            document.getElementById('slowspeed_button').onclick=function(){
                speed=0
            }
            document.getElementById('midspeed_button').onclick=function(){
                speed=0.0001
            }
            document.getElementById('highspeed_button').onclick=function(){
                speed=0.0005
            }


            function curvepoint(p1, p2) {
                return { x: (p2.x - p1.x) * offset + p1.x, y: (p2.y - p1.y) * offset + p1.y };
            }

            function drawline() {
            context3.strokeStyle = "#5bd4b0";
            context3.lineWidth = 4;
            context3.beginPath();

            final.forEach((item, index) => {
                if (index === 0) {
                    context3.moveTo(item.x, item.y)
                } else {
                    context3.lineTo(item.x, item.y)
                }
            });
            context3.stroke();
            }

            function drawlines(points,color) {
            if (points.length < 2) {
                const { x, y } = points[0];
                final.push({ x, y });
                
                drawline();
                return;
            }
            
            context3.lineWidth = 2;
            context3.beginPath();
            for (let i = 0; i < points.length; i++) {
                const { x, y } = points[i];
                if (i === 0) {
                    context3.moveTo(x, y);
                } else {
                    context3.lineTo(x, y);
                }
            }
            context3.strokeStyle= 'rgb(8, 103, 156,'+color+')'
            context3.stroke();
            const fpoints = [];
            for (let i = 0; i < points.length - 1; i++) {
                fpoints.push(curvepoint(points[i], points[i + 1]));
            }
            if(opacity>=5){
                drawlines(fpoints,color-0.95/(opacity-1))
            }else{
                drawlines(fpoints,color-0.2/(opacity-1))
            }
            
        }


            function draw(){
                if(drawstate){
                    if (points.length < 2) {
                        alert('多画几个点吧');
                        drawstate=true
                        canvas3.addEventListener('click',doo)
                        return;
                    }
                    drawlines(points,1);

                    const ani=()=>{
                        Interval=window.requestAnimationFrame(ani)
                        offset += speed
                        context3.clearRect(0, 0, canvas3.width, canvas3.height);
                        drawlines(points,1);
                        
                        if (offset >= 1) {
                            cancelAnimationFrame(Interval);
                            redraw=false
                        }
                    }

                    window.requestAnimationFrame(ani)


                    
                    drawstate=false
                }
            }
            
            function doo(e){
                points.push({x:e.offsetX,y:e.offsetY})
                context3.beginPath()
                context3.arc(e.offsetX,e.offsetY,2,0,2*Math.PI)
                context3.fillStyle='whitesmoke'
                context3.fill()
                opacity++
            }

            canvas3.addEventListener('click',doo)

            clear_button.addEventListener('click',()=>{
                context3.clearRect(0, 0, canvas3.width, canvas3.height);
                points = [];
                final = [];
                offset = 0;
                cancelAnimationFrame(Interval)
                drawstate=true
                opacity=0
                canvas3.addEventListener('click',doo)
            })
            draw_button.addEventListener('click',function(){
                canvas3.removeEventListener('click',doo)
                draw()
            })
            redraw_button.addEventListener("click", function () {
                if(!redraw){
                    final.length=0;
                    offset = 0;
                    context3.clearRect(0, 0, canvas3.width, canvas3.height);
                    drawstate=true
                    redraw=true
                    draw();
                }
                
            });

            function active(){
                document.querySelectorAll('.speed_active').forEach((item)=>{
                    item.style.backgroundColor='var(--gray)'
                    item.style.color='whitesmoke'
                    this.style.backgroundColor='#98C7E2'
                    this.style.color='var(--gray)'
                })
            }
                
            document.querySelectorAll('.speed_active').forEach((item)=>{
                item.addEventListener('click',active)
            })


        }
    }
</script>

