<script>
import inputt from './times/inputt.vue'


export default {
  components: { inputt },
  name: 'App',
    data(){
    return{
      n:-1,
      disabled:false,
      lists:[
        {
          name:'color-palette',
          contents:'一些算法',
          _lists:[
            {
              _name:'pulse',
              go:'/beziercurve',
              content:'贝塞尔曲线'
            }
            ,
            {
              _name:'analytics',
              go:'/perlinnoise',
              content:'柏林噪声'
            }
            ,
            {
              _name:'nutrition',
              go:'/simplexnoise',
              content:'单纯形噪声'
            }
            ,
            {
              _name:'airplane',
              go:'/devips',
              content:'变种群大小DE'
            }
            
            
          ]
        },
         {
          name:'planet',
          contents:'demos',
          _lists:[
            {
              _name:'git-commit',
              go:'/pnoise',
              content:'立体柏林噪声'
            }, {
              _name:'cloud-outline',
              go:'/snoise',
              content:'数字噪声?'
            },
            {
              _name:'radio',
              go:'/Kalmanfiltering',
              content:'卡卡卡尔曼滤波'
            },
            {
              _name:'finger-print',
              go:'/curves',
              content:'噪声曲线'
            }
            ,
            {
              _name:'timer',
              go:'/particular_b',
              content:'?...'
            }
          ]
        }, {
          name:'logo-game-controller-a',
          contents:'垃圾桶',
          _lists:[
            {
              _name:'logo-twitch',
              go:'/ev',
              content:'?...'
            }
          ]
        }
      ]
    }
  },
  methods:{
    nm(){

      if(sessionStorage['setting_h']==='false'){
        this.$refs.cur_ex.style.display='block'
        this.$refs.cur_in.style.display='block'
        this.$refs.app.style.cursor='none'
      }else{
        this.$refs.cur_ex.style.display='none'
        this.$refs.cur_in.style.display='none'
        this.$refs.app.style.cursor='auto'
      }
    },
    nmm() {
      var left = document.getElementById('left')
      if (!(JSON.parse(sessionStorage['setting_hl'] || false))) {
        
        document.getElementById('setting').className = 'settinghidden'

        left.className = 'lefthidden'
      } else {
left.classList.remove('lefthidden')
        document.getElementById('setting').classList.remove('settinghidden')
      }


    },
    noneclass() {
      document.getElementById('left').classList.add('ve')
    },
    expend() {
      const ev_content = document.getElementById('ev_content')
      const ev_canvas = document.getElementById('ev_canvas')
      const finaldone = document.getElementById('finaldone')
      if (ev_content && ev_canvas) {
        setTimeout(() => {
          ev_canvas.width = ev_content.offsetWidth
        },1100)
      }
      if (finaldone) {
        setTimeout(() => {
          finaldone.width = document.getElementById('rightfixed').offsetWidth
        },1100)
      }
      
    }
  },
  mounted() {
    $('#top').on('mouseout',()=>{
      $('#top_nav').css('display','none')
    })
    $('#top').on('mouseover',()=>{
      $('#top_nav').css('display','flex')
    })


    if((sessionStorage['setting_h'] || 'true')==='true'){
        this.$refs.cur_ex.style.display='block'
        this.$refs.cur_in.style.display='block'
        this.$refs.app.style.cursor='none'
      }else{
        this.$refs.cur_ex.style.display='none'
        this.$refs.cur_in.style.display='none'
        this.$refs.app.style.cursor='auto'

      }

      var left = document.getElementById('left')
      if ((JSON.parse(sessionStorage['setting_hl'] || false))) {
        
        document.getElementById('setting').className = 'settinghidden'

        left.className = 'lefthidden'
      } else {
left.classList.remove('lefthidden')
        document.getElementById('setting').classList.remove('settinghidden')
      }


    $('body').on("mouseout","a,button,label,.event-on,img,#setting,#setting_close",()=>{
      cur_in.classList.remove('cur_in_hover')
      cur_ex.classList.remove('cur_ex_hover')
      cur_in.classList.add('cur_in')
      cur_ex.classList.add('cur_ex')
    })
    window.addEventListener('mousemove',(e)=>{
      var e = e || window.event;
      var x=e.clientX
      var y=e.clientY
      cur_ex.style.transform='translate('+x+'px,'+y+'px)'
      cur_in.style.transform='translate('+x+'px,'+y+'px)'
    })
    $('body').on("mouseenter","a,button,label,.event-on,img,#setting,#setting_close",()=>{
      cur_in.classList.remove('cur_in')
      cur_ex.classList.remove('cur_ex')
      cur_in.classList.add('cur_in_hover')
      cur_ex.classList.add('cur_ex_hover')
    })

    $('#checkbox~ul span').css({"display":"none"})
    var checkbox_state=true
    $('#checkbox').on('click',()=>{
      if(checkbox_state){
        $('#checkbox~ul span').css({"display":"block"})
        checkbox_state=false
      }else{
        setTimeout(()=>{
        $('#checkbox~ul span').css({"display":"none"})
        checkbox_state=true
      },200)
      }
    })

    this.lists.forEach((item,index) => {
      item._lists.forEach(item=>{
        if(this.$route.path==item.go){
          this.n=index
        }
      })
    })

    var setting_close=document.getElementById('setting_close')
    var setting_window=document.getElementById('setting_window')
    var setting=document.getElementById('setting')
    setting_close.addEventListener('click',function(){
      setting_window.style.display='none'
    })
    setting.onclick=()=>{
      setting_window.style.display='block'
    }


    //监视top是否出现在视框内
    var obss = new IntersectionObserver(
    function(e){
      let _e = e[0]
      if(_e.isIntersecting) document.getElementById('top').className = 'add'
      else document.getElementById('top').className = 'none'
      },
      {
        thresholds:0.01
      }
    )
    obss.observe(document.getElementById('top'))






  },
  watch:{
    $route(){

    }
  }
}


</script>




