<template>
    <div id="pnoise">
        <div id="pnoise_body">
            <div>
                <canvas id="pnoisecanvas"></canvas>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'pnoise',
        data() {
            return {
                noiseinternal: 0,
                resize:0,
            }
    },
    mounted() {
        var that = this
        var pbody = document.getElementById('pnoise_body');
        var pnoisecanvas = document.getElementById('pnoisecanvas');
        var ctxpnoisecanvas = pnoisecanvas.getContext('2d');
        pnoisecanvas.width = pbody.clientWidth+200;
        pnoisecanvas.height = pbody.clientHeight;
        this.resize = () => {
            pnoisecanvas.width = pbody.clientWidth+200;
            pnoisecanvas.height = pbody.clientHeight;
        }
        window.addEventListener('resize', this.resize)


        //draw

        const table=[151,160,137,91,90,15,131,13,201,95,96,53,258,194,327,233,7,225,140,36,103,30,69,142,8,99,37,240,21,10,23,190,6,148,324,247,312,313,314,315,316,317,318,120,234,75,0,26,
                            197,62,94,252,219,203,117,35,11,32,57,177,33,88,237,149,56,87,174,20,125,136,171,168, 68,175,74,165,71,134,139,48,27,166,77,146,158,
                            231,83,111,229,122,60,321,211,133,230,220,105,92,41,55,46,245,40,244,102,143,54,65,25,63,161,1,216,80,73,209,76,132,187,208,89,346,301,18,169,
                            200,328,329,340,341,342,196,135,130,303,116,326,188,159,86,164,100,109,198,173,186,3,64,52,217,226,250,124,123,5,202,38,147,118,300,126,255,82,85,212,207,206,59,227,
                            47,16,58,17,182,189,28,42,223,183,170,213,119,248,152,2,44,319,154,163,256,325,70,221,153,304,305,306,307,308,101,155,167,43,172,9,129,22,39,253,19,98,108,110,79,113,
                            224,232,178,185,112,259,104,218,246,97,228,251,34,242,193,238,210,144,12,191,179,162,241,81,51,145,320,235,249,14,239,107,49,192,214,31,302,181,345,
                            199,106,157,309,310,311,184,84,204,176,115,121,323,50,45,127,344,4,150,254,138,347,236,205,322,93,222,114,67,29,24,343,72,243,141,128,195,257,78,66,215,61,156,180,

                            151,160,137,91,90,15,131,13,201,95,96,53,258,194,327,233,7,225,140,36,103,30,69,142,8,99,37,240,21,10,23,190,6,148,324,247,312,313,314,315,316,317,318,120,234,75,0,26,
                            197,62,94,252,219,203,117,35,11,32,57,177,33,88,237,149,56,87,174,20,125,136,171,168, 68,175,74,165,71,134,139,48,27,166,77,146,158,
                            231,83,111,229,122,60,321,211,133,230,220,105,92,41,55,46,245,40,244,102,143,54,65,25,63,161,1,216,80,73,209,76,132,187,208,89,346,301,18,169,
                            200,328,329,340,341,342,196,135,130,303,116,326,188,159,86,164,100,109,198,173,186,3,64,52,217,226,250,124,123,5,202,38,147,118,300,126,255,82,85,212,207,206,59,227,
                            47,16,58,17,182,189,28,42,223,183,170,213,119,248,152,2,44,319,154,163,256,325,70,221,153,304,305,306,307,308,101,155,167,43,172,9,129,22,39,253,19,98,108,110,79,113,
                            224,232,178,185,112,259,104,218,246,97,228,251,34,242,193,238,210,144,12,191,179,162,241,81,51,145,320,235,249,14,239,107,49,192,214,31,302,181,345,
                            199,106,157,309,310,311,184,84,204,176,115,121,323,50,45,127,344,4,150,254,138,347,236,205,322,93,222,114,67,29,24,343,72,243,141,128,195,257,78,66,215,61,156,180]
                function iplt(dx,x,y){
                    let t = 6 * dx * dx * dx * dx * dx - 15 * dx * dx * dx * dx + 10 * dx * dx * dx
                    return x+t*(y-x)
                }

                function getgradients(hash,x,y){
                    switch(hash & 7){
                        case 0: return y*2
                        case 1: return x + y
                        case 2: return x*2
                        case 3: return 2*x - y
                        case 4: return - y
                        case 5: return - x - 2*y
                        case 6: return - x*2
                        case 7: return - 2*x + y
                    }
                    
                }

                function tdnoise(x,y){
                    let bx = x - (x | 0)
                    let by = y - (y | 0)
                    
                    let floorx = x & 255
                    let floory = y & 255
                    
                    let n1 = table[floorx + table[floory]]
                    let n2 = table[floorx + 1 + table[floory]]
                    let n3 = table[floorx + table[floory + 1]]
                    let n4 = table[floorx + 1 + table[floory + 1]]

                    let m1 = getgradients(n1,bx,by)
                    let m2 = getgradients(n2,bx-1,by)
                    let m3 = getgradients(n3,bx,by-1)
                    let m4 = getgradients(n4,bx-1,by-1)

                    let r1 = iplt(bx,m1,m2)
                    let r2 = iplt(bx,m3,m4)

                    return iplt(by,r1,r2)
                }
        var y1 = 0.1
        function anipnoise() {
            that.noiseinternal = window.requestAnimationFrame(anipnoise)
            ctxpnoisecanvas.clearRect(0,0, pnoisecanvas.width, pnoisecanvas.height);



            for (let y = 0; y < 20; y++) {
                ctxpnoisecanvas.beginPath();
                ctxpnoisecanvas.moveTo(-10*y, pnoisecanvas.height/2 - 10*y);
                for (let x = 0; x < pnoisecanvas.width; x += 10) {
                    y1 += 0.000001
                    ctxpnoisecanvas.lineTo(x * 2 - 10*y, tdnoise(x / 80, y1+y/11) * 100 + pnoisecanvas.height/2 - 10*y);
                }
                ctxpnoisecanvas.strokeStyle = 'rgb(245,245,245,'+(1-y/20)+')';


                ctxpnoisecanvas.stroke();
            }


            for (let x = 0; x < pnoisecanvas.width; x+=10) {
                ctxpnoisecanvas.beginPath();
                ctxpnoisecanvas.moveTo(x*2,tdnoise(x / 80, y1) * 100 + pnoisecanvas.height/2)
                for (let y = 0; y < 20; y ++) {
                   ctxpnoisecanvas.lineTo(x * 2 - 10*y,tdnoise(x / 80, y1+y/11) * 100 + pnoisecanvas.height/2 - 10*y)
                }
                var trcol = ctxpnoisecanvas.createLinearGradient(x*2,tdnoise(x / 80, y1) * 100 + pnoisecanvas.height/2,x * 2 - 10*19,tdnoise(x / 80, y1+19/11) * 100 + pnoisecanvas.height/2 - 10*19)
                trcol.addColorStop(0, 'rgb(255,255,255,0.8)')
                trcol.addColorStop(1, 'rgb(0,0,0,0)')
                ctxpnoisecanvas.strokeStyle = trcol
                ctxpnoisecanvas.stroke()
            }

            
            
        }
        anipnoise()





    },
    beforeDestroy() {
        cancelAnimationFrame(this.noiseinternal)
        window.removeEventListener('resize', this.resize)
    }
    }
</script>

<style lang="" scoped>
    #pnoise_body{
        height: 100vh;
        overflow: hidden;
        z-index: -2;
    }
</style>


