<template>
    <div id="ev">
        <div id="ev_body">
            <div id="ev_content">
                <div id="put">
                    <div id="placement">
                        <div draggable="true" id="drag_target1">
                            <img src="../../assets/list1/pet.png" alt="2">
                        </div>
                        <div draggable="true" id="drag_target2">
                            <img src="../../assets/list1/pet.png" alt="2">
                        </div>
                        <div draggable="true" id="drag_target3">
                            <img src="../../assets/list1/pet.png" alt="2">
                        </div>
                    </div>
                </div>
                <canvas id="ev_canvas"></canvas>
            </div>
        </div>
        
    </div>
</template>

<script>
    export default {
        name: 'ev',
        data() {
            return {
                
            }
    },
    mounted() { 
        const ev_content = document.getElementById('ev_content')
        const ev_canvas = document.getElementById('ev_canvas')
        const ev_canvas_ctx = ev_canvas.getContext('2d')
        const put = document.getElementById('put')
        ev_canvas.width = ev_content.offsetWidth
        ev_canvas.height = ev_content.offsetHeight - put.offsetHeight


        let pets = []
        let foods = []
        let trees = []
        let food_size = 10 

        class food { 
            constructor(x,y) { 
                this.x = x
                this.y = y
                this.size = food_size
                this.state = 1

                this.draw = () => { 
                    if (this.state) { 
                        ev_canvas_ctx.fillStyle = '#ea481f'
                        ev_canvas_ctx.fillRect(x, y, this.size, this.size)
                    }
                }
                
            }
        }

        class tree{
            constructor(x,y) {
                this.x = x
                this.y = y
                this.count = 1

                this.draw = () => {
                    
                    ev_canvas_ctx.fillStyle = 'rgba(255,255,255, 0.555)'
                    ev_canvas_ctx.fillRect(this.x - 2, this.y - 2, 50, 50)
                    ev_canvas_ctx.fillStyle = '#1ba784'
                    ev_canvas_ctx.fillRect(this.x, this.y, 50, 50)
                    ev_canvas_ctx.fillStyle = '#f2ac20'
                    ev_canvas_ctx.fillRect(this.x + 10, this.y + 20, 30 * (this.count % 100) /100, 8)
                    if (this.count % 100 == 0) {
                        foods.push(new food(Math.random() * (ev_canvas.width), Math.random() * (ev_canvas.height)))
                    }
                    this.count++
                }

                this.generate = () => {
                    setInterval(() => {
                        
                    },1000)
                }

                
            }
        }


        class pet { 
            constructor(x, y, ate, r, g, b, speed) { 

                this.ate_intial = ate
                this.x = x
                this.y = y
                this.sx = 0
                this.sy = 0
                this.scale = 1
                this.size = 30 * this.scale
                this.ate = ate
                this.r = r
                this.g = g
                this.b = b
                this.speed = speed

                let mind = 1

                this.move = function move() { 
                    foods.forEach((f) => {
                        if (f.state) {
                            const right = (f.x + f.size) >= this.x && (f.x + f.size)<=(this.x+this.size)
                            const left = (this.x + this.size) >= f.x && this.x <= f.x
                            const top = f.y >= this.y && f.y <= (this.y + this.size)
                            const bottom = (f.y+f.size)>=this.y&&(f.y+f.size)<=(this.y + this.size)
                            if ((left&&top)||(right&&top)||(left&&bottom)||(right&&bottom)) { 
                                f.state = 0
                                this.ate = this.ate_intial
                            }
                    }
                    })
                    let distance = []
                    let direction = []
                    foods.forEach((f) => {
                        if (f.state) {
                            const tx = f.x + f.size / 2 - this.x - this.size / 2
                            const ty = f.y + f.size / 2 - this.y - this.size / 2
                            distance.push(tx * tx + ty * ty)
                            direction.push([tx , ty])
                        }
                    })

                    mind = distance[0]
                    let minindex = 0
                    distance.forEach((item, index) => {
                        if (item < mind) {
                            mind = item
                            minindex = index
                        }
                    })

                    this.sx = this.speed * ((direction[minindex] || [])[0] / Math.sqrt(mind) ) /2 || 0
                    this.sy = this.speed * ((direction[minindex] || [])[1] / Math.sqrt(mind) ) /2 || 0

                    this.x += this.sx
                    this.y += this.sy

                    this.ate -= this.speed/100
                }

                this.position = function position() {
                    let m = 0
                    let n = 0
                    if (this.sx > 0) {
                        m = 5
                    } else { 
                        m = 0
                    }
                    if (this.sy > 0) { 
                        n = 5
                    }

                    if (this.ate > 0) {
                        //血条
                        ev_canvas_ctx.fillStyle = 'rgb(' + this.r + ',' + this.g + ','+ 237 +')'
                        ev_canvas_ctx.fillRect(this.x, this.y - 10, this.size * this.ate / this.ate_intial, 5)
                        
                        ev_canvas_ctx.fillRect(this.x, this.y, this.size, this.size)
                        ev_canvas_ctx.fillStyle = 'whitesmoke'
                        ev_canvas_ctx.fillRect(this.x + (5 + m)*this.scale, this.y + (5 + n)*this.scale, 6*this.scale, 15*this.scale)
                        ev_canvas_ctx.fillRect(this.x + (15 + m) * this.scale, this.y + (5 + n) * this.scale, 6 * this.scale, 15 * this.scale)
                        this.move()
                    }
                }
            }
            
        }


        //初始化食物
        for (let i = 0; i < 200; i++) { 
            foods.push(new food(Math.random() * (ev_canvas.width), Math.random() * (ev_canvas.height)))
        }


        //点击放置食物
        ev_canvas.addEventListener('click', (e) => {
            const x = e.offsetX
            const y = e.offsetY
            foods.push(new food(x - food_size / 2, y - food_size / 2))
        })

        //拖拽放置
        let drag_now = undefined
        const placement1 = document.getElementById('drag_target1')
        const placement2 = document.getElementById('drag_target2')
        const placement3 = document.getElementById('drag_target3')
        placement1.addEventListener('dragstart', () => {
            drag_now = 1
        })
        placement2.addEventListener('dragstart', () => {
            drag_now = 2
        })
        placement3.addEventListener('dragstart', () => {
            drag_now = 3
        })
        ev_canvas.addEventListener('dragover', (e) => {
            e.preventDefault()//需要两个
        })
        ev_canvas.addEventListener('drop', (e) => {
            const x = e.offsetX
            const y = e.offsetY
            e.preventDefault()//需要两个
            if (drag_now == 1) {
                pets.push(new pet(x, y, 2, 100, 149, 237, 0.5))
            } else if (drag_now == 2) {
                pets.push(new pet(x, y, 1, 59, 26, 250, 1))
            } else if (drag_now == 3) {
                trees.push(new tree(x, y))
            }
        })


        //绘制
        function run() { 
            window.requestAnimationFrame(run)
            ev_canvas_ctx.clearRect(0, 0, ev_canvas.width, ev_canvas.height)

            for (let i of trees) { 
                i.draw()
            }

            foods.forEach((f) => { 
                f.draw()
            })
                
            
            for (let i of pets) { 
                i.position()
                
            }
        }
        run()
        
    }
    }
</script>

<style coped>
    #ev_body{
        overflow: hidden;
    }
    #ev_content{
        height: 100vh;
    }
    #put{
        height: 60px;
        background-color: rgb(251, 207, 119);
    }
    #placement{
        height: 100%;
        display: flex;
        align-items: center;
        user-select: none;
    }
    #placement img{
        pointer-events: none;
    }
    .drag_target{
        background-color: black;
    }
</style>


