<template>
    <div class="KalmanFiltering">
        <div id="KalmanFiltering_content">
            <div id="KalmanFiltering_body">
                
                <div id="KalmanFiltering_left">
                    <div id="KalmanFiltering_ture">
                    <div class="decora"></div>
                    
                    <div id="flexb">
                        <div>
                            <span class="rowone">实际位置</span>
                            <span>X1</span>
                            <span v-for="(list,i) in trueposition" :key="i" v-text="list"></span>
                        </div>
                        <div>
                            <span class="rowone">实际速度</span>
                            <span>X2</span>
                            <span v-for="(list,i) in truespeed" :key="i" v-text="list"></span>
                        </div>
                        <div>
                            <span class="rowone">实际位置过程误差</span>
                            <span>A1</span>
                            <span v-for="(list,i) in turepositiondeviation" :key="i" v-text="list"></span>
                        </div>
                        <div>
                            <span class="rowone">实际速度过程误差</span>
                            <span>A2</span>
                            <span v-for="(list,i) in turespeeddeviation" :key="i" v-text="list"></span>
                        </div>
                        <div>
                            <span class="rowone">实际位置过程误差方差</span>
                            <div class="inputdiv" style="border-right: 0.1px rgba(255, 255, 255, 0.193) solid ;"><input type="number" min="0.01" step="0.01" class="inputvs" v-model="turepositiondeviationvalue"></div>
                        </div>
                        <div class="colomnlast">
                            <span class="rowone">实际速度过程误差方差</span>
                            <div class="inputdiv"><input type="number" min="0.01" step="0.01" value="1" class="inputvs" v-model="turespeeddeviationvalue"></div>
                        </div>
                    </div>

                    
                    
                </div>

                <div id="KalmanFiltering_cal">
                    <div class="decora"></div>
                    
                    <div id="flexa">
                        <div>
                            <span class="rowone">测量位置</span>
                            <span>Y1</span>
                            <span v-for="(list,i) in calpositionarray" :key="i" v-text="list"></span>
                        </div>
                        <div>
                            <span class="rowone">测量速度</span>
                            <span>Y2</span>
                            <span v-for="(list,i) in calspeedarray" :key="i" v-text="list"></span>
                        </div>
                        <div>
                            <span class="rowone">位置测量误差</span>
                            <span>C1</span>
                            <span v-for="(list,i) in calpositiondeviation" :key="i" v-text="list"></span>
                        </div>
                        <div>
                            <span class="rowone">速度测量误差</span>
                            <span>C2</span>
                            <span v-for="(list,i) in calspeeddeviation" :key="i" v-text="list"></span>
                        </div>
                        <div>
                            <span class="rowone">位置测量误差方差</span>
                            <div class="inputdiv" style="border-right: 0.1px rgba(255, 255, 255, 0.193) solid ;"><input type="number" min="0.01" step="0.01" class="inputvs" v-model="calpositiondeviationvalue"></div>
                        </div>
                        <div class="colomnlast">
                            <span class="rowone">速度测量误差方差</span>
                            <div class="inputdiv"><input type="number" min="0.01" step="0.01" value="1" class="inputvs" v-model="calspeeddeviationvalue"></div>
                        </div>
                    </div>
                    
                    
                </div>

                <div id="KalmanFiltering_att">
                    <div class="decora"></div>
                    <div id="flexc">
                        <div>
                            <span class="rowone">序号</span>
                            <span v-for="(list,i) in num" :key="i" v-text="list" :class="{'fuc':i%2!=0}"></span>
                        </div>
                        <div>
                            <span class="rowone" style="min-width:140px">Pk</span>
                            <div v-for="(list,i) in pk" :key="i">
                                <span v-text="list.x1" class="colomnspe"></span>
                                <span v-text="list.x2" class="colomnspe"></span>
                                <span v-text="list.x3" class="colomnspe"></span>
                                <span v-text="list.x4" class="colomnspe"></span>
                            </div>
                            
                        </div>
                        <div>
                            <span class="rowone" style="min-width:140px">Pk-</span>
                            <div v-for="(list,i) in pk_" :key="i">
                                <span v-text="list.x1" class="colomnspe"></span>
                                <span v-text="list.x2" class="colomnspe"></span>
                                <span v-text="list.x3" class="colomnspe"></span>
                                <span v-text="list.x4" class="colomnspe"></span>
                            </div>
                        </div>
                        <div>
                            <span class="rowone" style="min-width:140px">Kk</span>
                            <div v-for="(list,i) in kk" :key="i">
                                <span v-text="list.x1" class="colomnspe"></span>
                                <span v-text="list.x2" class="colomnspe"></span>
                                <span v-text="list.x3" class="colomnspe"></span>
                                <span v-text="list.x4" class="colomnspe"></span>
                            </div>
                        </div>
                    </div>
                </div>

                <div id="KalmanFiltering_y">
                    <div class="decora"></div>
                    <div id="flexd">
                        <div>
                            <span class="rowone">先验估计位置</span>
                            <span>X1^-</span>
                            <span v-for="(list,i) in xk_" :key="i" v-text="list.x1"></span>
                        </div>
                        <div>
                            <span class="rowone">先验估计速度</span>
                            <span>X2^-</span>
                            <span v-for="(list,i) in xk_" :key="i" v-text="list.x3"></span>
                        </div>
                        <div>
                            <span class="rowone">后验估计位置</span>
                            <span>X1^</span>
                            <span v-for="(list,i) in xk" :key="i" v-text="list.x1"></span>
                        </div>
                        <div>
                            <span class="rowone">后验估计速度</span>
                            <span>X2^</span>
                            <span v-for="(list,i) in xk" :key="i" v-text="list.x3"></span>
                        </div>
                    </div>
                </div>

                </div>
                <div id="KalmanFiltering_rigth">
                    <div id="rightfixed">
                        <div class="decora"></div>
                        <div id="rightfixed_top">
                            <div id="amat">
                                <span>状态A矩阵</span>
                                <div>
                                    <input type="number" value="1" readonly>
                                    <input type="number" min="0.01" step="0.1" v-model="time">
                                    <input type="number" value="0" readonly>
                                    <input type="number" value="1" readonly>
                                </div>
                            </div>
                            <div id="bmat">
                                <span>误差B矩阵</span>
                                <div>
                                    <input type="number" v-model="turepositiondeviationvalue" step="0.01" min="0.01">
                                    <input type="number" v-model="matrixB_con2" step="0.01">
                                    <input type="number" v-model="matrixB_con2" step="0.01">
                                    <input type="number" v-model="turespeeddeviationvalue" step="0.01" min="0.01">
                                </div>
                            </div>
                            <div id="cmat">
                                <span>误差C矩阵</span>
                                <div>
                                    <input type="number" v-model="calpositiondeviationvalue" min="0.01" step="0.01">
                                    <input type="number" step="0.01" v-model="matrixD_con1">
                                    <input type="number" step="0.01" v-model="matrixD_con1">
                                    <input type="number" v-model="calspeeddeviationvalue" min="0.01" step="0.01">
                                </div>
                            </div>
                            <div id="dmat">
                                <span>Pk0矩阵</span>
                                <div>
                                    <input type="number" v-model="pk1" step="0.01" @input="qjt(),yyyy()" min="0">
                                    <input type="number" v-model="pk2" step="0.01" @input="qjt(),yyyy()">
                                    <input type="number" v-model="pk2" step="0.01" @input="qjt(),yyyy()" min="0">
                                    <input type="number" v-model="pk4" step="0.01" @input="qjt(),yyyy()">
                                </div>
                            </div>
                            <div id="initkaiman">
                                <div>
                                    <span>加速度:</span>
                                    <input type="number" v-model="acc" step="0.01">
                                </div>
                                <div>
                                    <span>时间间隔:</span>
                                    <input type="number" min="0.01" step="0.1" v-model="time">
                                </div>
                                <div>
                                    <span>初速度:</span>
                                    <input type="number" step="1" v-model="v0">
                                </div>
                                <div>
                                    <span>模拟次数:</span>
                                    <input type="number" step="1" min="3" v-model="truedata">
                                </div>
                            </div>
                        </div>

                        <div id="canvasoutput">
                            <canvas id="finaldone"></canvas>
                        </div>
                    </div>
                </div>
                
                
            </div>
        </div>
    </div>
</template>

<script>
import { nextTick } from 'vue'
export default {
    name: 'KalmanFiltering',
    data() {
        return {
            dont:null,
            observerp: null,
            pplpllp: null,
            acc: 0,
            time: 1,
            v0: 1,
            truedata: 40,
            trueposition: [],
            truespeed: [],
            turepositiondeviation: [],
            turespeeddeviation: [],
            turepositiondeviationvalue: 1,
            turespeeddeviationvalue: 1,
            calpositiondeviation: [],
            calspeeddeviation: [],
            calpositiondeviationvalue: 1,
            calspeeddeviationvalue: 1,
            calpositionarray: [],
            calspeedarray: [],
            num: [],
            pk: [],
            pk1: 1,
            pk2: 0,
            pk4: 1,
            pk_: [],
            kk: [],
            matrixA: {},
            matrixB: {},
            matrixB_con2: 0,
            matrixD: {},
            matrixD_con1: 0,
            xk_: [],
            xk: [],
            tlzone: [],//图例区域
            tlarray: [],//图例点击状态
        }
    },
    mounted() { 



        var that = this
        //调整rowone高度统一
        let rowonelist = document.querySelectorAll('#flexb .rowone')
        let rowonelistheight = 0

        function heisuit(rowonelist) {
            rowonelist.forEach((item) => {
                const hh = item.getBoundingClientRect().height
                hh > rowonelistheight ? rowonelistheight = hh : 0
            });

            rowonelist.forEach((item) => {
                item.style.height = rowonelistheight + 'px'
            })
        }
        heisuit(rowonelist)
        heisuit(document.querySelectorAll('#flexa .rowone'))
        /////////


        //运行整体函数----------------------------------------------------------------
        this.all()



        //画布部分---------------------------------------------------------------

        const finaldone = document.getElementById('finaldone')
        const fictx = finaldone.getContext('2d')

        finaldone.height = Math.min(document.getElementById('KalmanFiltering_rigth').offsetHeight - 10,558)
        finaldone.width = document.getElementById('KalmanFiltering_rigth').offsetWidth

        this.pplpllp = () => {
            this.$nextTick(() => {
                finaldone.height = Math.min(document.getElementById('KalmanFiltering_rigth').offsetHeight - 10,558)
                finaldone.width = document.getElementById('KalmanFiltering_rigth').offsetWidth
            })
        }

        window.addEventListener('resize',this.pplpllp)

        //求数组极大极小值
        function findu(arr,o) {
            let minf = arr[0][o] || arr[0]
            let maxf = arr[0][o] || arr[0]

            arr.forEach((item) => {
                let i = item[o] || item
                if (i > maxf) {
                    maxf = i
                }
                if (i < minf) {
                    minf = i
                }
            })

            return { max: maxf, min: minf }
        }



//------------------------------------------------------------------------大量监听
        let statec = true //使点过近时悬停效果只能发生一次
        let liszone = {}//记录鼠标进入区域，当离开时可触发
        
        //监听鼠标悬停画布
        let xx
        let yy
        let io//鼠标是否在画布上
        let stt = true
        finaldone.addEventListener('mouseenter', (e) => {
            stt = true
            io = true
        })
        finaldone.addEventListener('mousemove', (e) => {
            
            xx = e.offsetX
            yy = e.offsetY

            if (Math.abs(xx - liszone.x1) > 6 ) {
                //当鼠标离开点区域时重置效果
                statec = true
            }
        })

        finaldone.addEventListener('mouseout', () => {
            io = false
            stt = false
        },false)


        //画布点悬停效果
        let colomn //使整列产生效果
        let movetarget  //移动渐变参数
        function can(x, y, color) {
            if (that.tlarray[4]) {
                if (Math.abs(xx - x) <= 6 && statec) {
                    movetarget = colomn || x
                    colomn = x

                    statec = false
                    liszone = { x1: x, y1: y }
                }
                return
            }

            if (Math.abs(x - colomn) <= 1) {//使整列产生效果
                
                if (!stt) {
                    fictx.fillRect(x-3,y-3,6,6)
                }
                if (!that.tlarray[4] && stt) {
                    fictx.fillStyle = 'rgb(245,245,245)'
                    fictx.fillRect(x - 4, y - 4, 8, 8)
                }
                    


            }
            else if (Math.abs(xx - x) <= 6 && statec) {
                fictx.fillStyle = 'rgb(245,245,245)'
                if(!that.tlarray[4])
                    fictx.fillRect(x - 4, y - 4, 8, 8)

                movetarget = colomn || x
                colomn = x
                
                statec = false
                liszone = { x1: x, y1: y }
            } else {
                fictx.fillStyle = color
                if (!that.tlarray[4])
                fictx.fillRect(x-3,y-3,6,6)
            }
        }

        //图例点击状态
        this.tlarray = [true, true, true, true, false, true]
            
        //画布图例点击

        finaldone.addEventListener('click', (e) => {
            const xxx = e.offsetX
            const yyy = e.offsetY

            that.tlzone.forEach((item, index) => {
                if (xxx >= item.zonex1 && xxx <= item.zonex2 && yyy <= item.zoney1 && yyy >= item.zoney2) {
                    that.tlarray[index] = !that.tlarray[index]
                }
            })

            if (xxx >= that.tlzone[5].zonex1 && xxx <= that.tlzone[5].zonex2 && yyy <= that.tlzone[5].zoney1 && yyy >= that.tlzone[5].zoney2) {
                let d = that.tlarray[5]
                that.tlarray = [true, true, true, true, false, true]
                that.tlarray[5] = d
            }

            
        })

        function dfdf(a,b,c,d,fl,wz) {
            //取消阴影
            fictx.shadowBlur = 0
            fictx.shadowColor = ''
            fictx.clearRect(0, 0, finaldone.width, finaldone.height)

            const target = movetarget - colomn
            if (Math.abs(target) > 0.5) {
                movetarget -= target / 10
            }
            
            if (io && (that.tlarray[0] || that.tlarray[1] || that.tlarray[2] || that.tlarray[3])) {
                fictx.beginPath()
                fictx.lineWidth = 1
                fictx.setLineDash([10,10])
                fictx.strokeStyle = 'rgb(255,255,255,0.3)'
                fictx.moveTo(movetarget, 30)
                fictx.lineTo(movetarget, finaldone.height - 20)
                fictx.stroke()
                fictx.closePath()
                fictx.setLineDash([1,0])
            }
            //电脑端可直接用[0]设为实线，手机端需要[1,0]

            
            
            
            //图例区域
            that.tlzone.length = 0

            function tl(x, y, color, text,index) {
                const row = x % finaldone.width
                const colomn = 25 * (x / finaldone.width | 0)
                const colomnuse = y + 2.5 + colomn
                const widthuse = row + 10 + fictx.measureText(text).width
                fictx.fillStyle=color
                fictx.fillRect(row, y + colomn, 5, 5)
                fictx.font = '16px Roboto Mono'
                fictx.textAlign = 'left'
                fictx.fillText(text, row + 10, colomnuse)
                that.tlzone.push({ zonex1: row, zonex2: widthuse, zoney1: colomnuse, zoney2: colomnuse - 16 })

                if(index == 5) return

                if (!that.tlarray[index]) {     //是否画线穿过文字
                    fictx.lineWidth = 1
                    fictx.strokeStyle = 'rgb(255,255,255)'
                    fictx.beginPath()
                    fictx.moveTo(row, colomnuse - 10)
                    fictx.lineTo(widthuse, colomnuse)
                    fictx.stroke()

                    fictx.beginPath()
                    fictx.moveTo(row, colomnuse)
                    fictx.lineTo(widthuse, colomnuse - 10)
                    fictx.stroke()
                }
            }

            tl(finaldone.width / 10, 20, 'rgb(68, 115, 255)', ('实际'+wz),0)
            tl(finaldone.width / 10 + 100, 20, 'rgb(91, 212, 176)', ('测量'+wz),1)
            tl(finaldone.width / 10 + 200, 20, '#f2ac20', ('先验'+wz),2)
            tl(finaldone.width / 10 + 300, 20, '#ea481f', ('后验'+wz), 3)
            tl(finaldone.width / 10 + 400, 20, '#FE7D66', '隐藏节点', 4) //是否隐藏节点
            tl(finaldone.width / 10 + 500, 20, 'rgb(245,245,245)', wz + '模式',5) //切换模式


            //寻找所有曲线极大极小值
            let qnmd = []

                d.forEach((item) => {
                    qnmd.push(item[fl])
                })



            let find

                find = [findu(a), findu(b), findu(c.slice(1,c.length),fl), findu(qnmd,fl)]

            

            let minf = find[0].min
            let maxf = find[0].max
            find.forEach((item) => {
                minf = item.min < minf ? item.min : minf
                maxf = item.max > maxf ? item.max : maxf
            })


            //画布大小
            const zonex = finaldone.width - 40
            const zoney = finaldone.height - 50

            //x轴间隔
            const xgap = zonex / that.truedata

            // //缩放比
            const ctxratio = zoney / (maxf - minf)

            // //确定原点位置
            var dotbe
            if (maxf >= 0 && minf <= 0) {
                dotbe = zoney * (maxf) / (maxf - minf)
            } else if (maxf < 0) {
                dotbe = -maxf * ctxratio
            } else {
                dotbe = zoney + minf * ctxratio
            }

            fictx.lineCap = 'round'
            fictx.lineJoin = 'round'
            fictx.lineWidth = 1

            //标y轴
            fictx.beginPath()
            fictx.font = '10px Roboto Mono'
            fictx.strokeStyle = 'rgb(255,255,255,0.1)'
            fictx.fillText(0, 5, dotbe + 10)
            fictx.moveTo(20, dotbe + 30)
            fictx.lineTo(finaldone.width - 10, dotbe + 30)
            fictx.stroke()

            let count = 0
            for (let y = dotbe + 30 - 100; y >= 30; y -= 100){
                count++
                fictx.beginPath()
                fictx.fillText((100 * count/ctxratio).toFixed(1), 5, y - 20)
                fictx.moveTo(20, y)
                fictx.lineTo(finaldone.width - 10, y)
                fictx.stroke()
            }

            count = 0
            for (let y = dotbe + 130; y < finaldone.height - 20; y += 100){
                count++
                fictx.beginPath()
                fictx.fillText((-100 * count/ctxratio).toFixed(1), 5, y - 20)
                fictx.moveTo(20, y)
                fictx.lineTo(finaldone.width - 10, y)
                fictx.stroke()
            }

            fictx.lineWidth = 2

            //标x轴序号
            for (let index = 0; index < that.truedata + 1; index++){
                fictx.textAlign = 'center'
                fictx.fillStyle = 'rgb(255,255,255,0.9)'
                fictx.font = Math.max(10 - (that.truedata / 60 | 0) * 2, 1) + 'px Roboto Mono'
                fictx.fillText(index + 1, 30 + xgap * index, finaldone.height - 10)
                fictx.fillStyle = 'rgb(68, 115, 255)'
            }
            

                    
            //实际位置            
            
            if (that.tlarray[0]) {
                fictx.beginPath()
                fictx.strokeStyle = 'rgb(68, 115, 255)'
                
                a.forEach((item, index) => {
                    const x = 30 + xgap * index
                    const y = dotbe + 30 - item * ctxratio

                    fictx.lineTo(x, y)
                    can(x, y,'rgb(68, 115, 255)')


                })
                fictx.stroke()
            }


            //测量位置
            if (that.tlarray[1]) {
                fictx.beginPath()
                fictx.strokeStyle = 'rgb(91, 212, 176)'
                b.forEach((item, index) => {
                    const x = 30 + xgap * index + xgap
                    const y = dotbe + 30 - item * ctxratio

                    fictx.lineTo(x, y)
                    can(x,y,'rgb(91, 212, 176)')
                    
                })
                fictx.stroke()
            }
            


            //先验位置
            if (that.tlarray[2]) {
                fictx.beginPath()
                fictx.strokeStyle = '#f2ac20'
                c.slice(1, c.length).forEach((item, index) => {
                    const x = 30 + xgap * index + xgap
                    const y = dotbe + 30 - item[fl] * ctxratio

                    fictx.lineTo(x, y)
                    can(x, y,'#f2ac20')

                })
                fictx.stroke()
            }


            //后验位置
            if (that.tlarray[3]) {
                fictx.beginPath()
                fictx.strokeStyle = '#ea481f'
                qnmd.slice(1, qnmd.length).forEach((item, index) => {
                    const x = 30 + xgap * index + xgap
                    const y = dotbe + 30 - item * ctxratio

                    fictx.lineTo(x, y)
                    can(x, y,'#ea481f')

                })
                fictx.stroke()
            }


            //移动浮标
            if (io && (that.tlarray[0] || that.tlarray[1] || that.tlarray[2] || that.tlarray[3])) {
                fictx.fillStyle = 'red'
                const index = Math.round((colomn - 30)/xgap)
                let left_top_x = movetarget + 20
                let left_top_y = yy + 20
                fictx.font = '16px Roboto Mono'

                if (index) {
                    const s1 = '实际'+wz+':' + a[index]
                const s2 = '测量'+wz+':' + b[index - 1]
                const s3 = '先验'+wz+':' + c[index][fl]
                const s4 = '后验'+wz+':' + d[index][fl]
                const s5 = '绝对误差:' + (d[index][fl] - a[index]).toFixed(2)
                const s6 = ('相对误差:' + (((d[index][fl] - a[index]).toFixed(2) / a[index]).toFixed(4) * 100).toFixed(2) + '%')

                
                const width = Math.max.apply(null, [fictx.measureText(s1).width, fictx.measureText(s2).width, fictx.measureText(s3).width, fictx.measureText(s4).width,fictx.measureText(s5).width,fictx.measureText(s6).width])
                fictx.shadowBlur = 5
                fictx.fillStyle = 'rgb(255,255,255)'
                fictx.shadowColor = 'rgb(0,0,0,0.5)'
                if (left_top_x + width + 40 >= finaldone.width) {
                    left_top_x -= (width + 60)
                }
                if (left_top_y + 150 >= finaldone.height) {
                    left_top_y -= 190
                }
                fictx.fillRect(left_top_x, left_top_y, width + 20, 130)
                fictx.textAlign = 'left'
                fictx.shadowColor = ''
                fictx.shadowBlur = 0
                fictx.fillStyle = 'rgb(35,35,35)'
                fictx.fillText(s1, left_top_x + 10, left_top_y + 20)
                fictx.fillText(s2, left_top_x + 10, left_top_y + 40)
                fictx.fillText(s3, left_top_x + 10, left_top_y + 60)
                fictx.fillText(s4, left_top_x + 10, left_top_y + 80)
                fictx.fillText(s5, left_top_x + 10, left_top_y + 100)
                fictx.fillText(s6,left_top_x + 10, left_top_y+120)
                }

                
            }


            
        }

        function drawkalman() {
            that.observerp = window.requestAnimationFrame(drawkalman)
            if(that.tlarray[5])
                dfdf(that.trueposition, that.calpositionarray, that.xk_, that.xk, 'x1', '位置')
            else
                dfdf(that.truespeed,that.calspeedarray,that.xk_,that.xk,'x3','速度')
        }
        drawkalman()
    },
    watch: {
        turepositiondeviationvalue() {
            this.turepositiondeviation.length = 0
            for (let x = 0; x < Number(this.truedata) + 1; x++) {
                var gaussian = require('gaussian');
                var distribution = gaussian(0, this.turepositiondeviationvalue);
                this.turepositiondeviation.push(Math.round(distribution.ppf(Math.random()) * 100) / 100);
            }
            this.caltrueposition()
            this.vposition()
            this.yyyy()
        },
        turespeeddeviationvalue() {
            this.turespeeddeviation.length = 0
            for (let x = 0; x < Number(this.truedata) + 1; x++) {
                var gaussian = require('gaussian');
                var distribution = gaussian(0, this.turespeeddeviationvalue);
                this.turespeeddeviation.push(Math.round(distribution.ppf(Math.random()) * 100) / 100);
            }
            this.calstruespeed()
            this.caltrueposition()
            this.vspeed()
            this.vposition()
            this.yyyy()
        },
        calpositiondeviationvalue() {
            this.calpositiondeviation.length = 0
            for (let x = 0; x < Number(this.truedata); x++) {
                var gaussian = require('gaussian');
                var distribution = gaussian(0, this.calpositiondeviationvalue);
                this.calpositiondeviation.push(Math.round(distribution.ppf(Math.random()) * 100) / 100);
            }
            this.vposition()
            this.yyyy()
        },
        calspeeddeviationvalue() {
            this.calspeeddeviation.length = 0
            for (let x = 0; x < Number(this.truedata); x++) {
                var gaussian = require('gaussian');
                var distribution = gaussian(0, this.calspeeddeviationvalue);
                this.calspeeddeviation.push(Math.round(distribution.ppf(Math.random()) * 100) / 100);
            }
            this.vspeed()
            this.yyyy()
        },
        acc() {
            this.all()
        },
        time() {
            this.all()
        },
        v0() {
            this.all()
        },
        truedata() {
            this.all()
        },
        matrixD_con1() {
            this.matrixD = { x1: Number(this.calpositiondeviationvalue), x2: Number(this.matrixD_con1), x3: Number(this.matrixD_con1), x4: Number(this.calspeeddeviationvalue) }
            this.qjt()
            this.yyyy()
        },
        matrixB_con2() {
            this.matrixB = { x1: Number(this.turepositiondeviationvalue), x2: Number(this.matrixB_con2), x3: Number(this.matrixB_con2), x4: Number(this.turespeeddeviationvalue) }
            this.qjt()
            this.yyyy()
        }
    }, methods: {
        calstruespeed() {
            this.truespeed.length = 0;
            this.truespeed.push(Number(this.v0))
            for (let x = 1; x < Number(this.truedata) + 1; x++) {
                this.truespeed[x] = Number((this.truespeed[x - 1] + Number(this.acc) * Number(this.time) + this.turespeeddeviation[x - 1]).toFixed(2))
            }
        },
        caltrueposition() {
            this.trueposition.length = 0
            this.trueposition.push(0)
            for (let x = 1; x < Number(this.truedata) + 1; x++) {
                this.trueposition[x] = Number((this.trueposition[x - 1] + this.truespeed[x - 1] * this.time + 1 / 2 * Number(this.acc) * Number(this.time) * this.time + this.turepositiondeviation[x - 1]).toFixed(2))
            }

        },
        //计算测量位置
        vposition() {
            this.calpositionarray.length = 0
            for (let x = 0; x < Number(this.truedata); x++) {
                this.calpositionarray[x] = Number((this.trueposition[x + 1] + this.calpositiondeviation[x]).toFixed(2))
            }
        },
        //计算测量位置
        vspeed() {
            this.calspeedarray.length = 0
            for (let x = 0; x < Number(this.truedata); x++) {
                this.calspeedarray[x] = Number((this.truespeed[x + 1] + this.calspeeddeviation[x]).toFixed(2))
            }
        },
        //计算pk
        calpk(x) {
            this.pk.push(this.rounnnd(this.mult(this.matrixsubtract({ x1: 1, x2: 0, x3: 0, x4: 1 }, this.kk[x + 1]), this.pk_[x + 1])))
        },
        //计算Pk-
        calpk_(x) {
            this.pk_.push(this.rounnnd(this.matrixplus(this.mult(this.mult(this.matrixA, this.pk[x]), this.ud(this.matrixA)), this.matrixB)))
        },
        //计算Kk
        calkk(x) {
            this.kk.push(this.rounnnd(this.mult(this.pk_[x+1],this.inversematrix(this.matrixplus(this.pk_[x+1],this.matrixD)))))
        },
        //矩阵乘法定义
        mult(m, n) {
            return { x1: m.x1 * n.x1 + m.x2 * n.x3, x2: m.x1 * n.x2 + m.x2 * n.x4, x3: m.x3 * n.x1 + m.x4 * n.x3, x4: m.x3 * n.x2 + m.x4 * n.x4 }
        },
        //矩阵转置函数
        ud(matrix) {
            return { x1: matrix.x1, x2: matrix.x3, x3: matrix.x2, x4: matrix.x4 }
        },
        //矩阵加法函数
        matrixplus(m, n) {
            return { x1: m.x1 + n.x1, x2: m.x2 + n.x2, x3: m.x3 + n.x3, x4: m.x4 + n.x4 }
        },
        //矩阵减法函数
        matrixsubtract(m,n) {
            return { x1: m.x1 - n.x1, x2: m.x2 - n.x2, x3: m.x3 - n.x3, x4: m.x4 - n.x4 }
        },
        //逆矩阵函数
        inversematrix(m) {
            const mn = m.x1 * m.x4 - m.x2 * m.x3
            return { x1: Number((m.x4 / mn).toFixed(2)), x2: -1 * Number((m.x2 / mn).toFixed(2)), x3: -1 * Number((m.x3 / mn).toFixed(2)), x4: Number((m.x1 / mn).toFixed(2)) }
        },
        //小数部分舍弃函数
        rounnnd(m) {
            const ws = 2
            return { x1: Number((m.x1).toFixed(ws)), x2: Number((m.x2).toFixed(ws)), x3: Number((m.x3).toFixed(ws)), x4: Number((m.x4).toFixed(ws)) }
        },
        //pk,kk,pk_全家桶计算
        qjt() {
            this.pk.length = 0
            this.pk.push({ x1: Number(this.pk1), x2: Number(this.pk2), x3: Number(this.pk2), x4: Number(this.pk4) })

            this.pk_.length = 0
            this.pk_.push({ x1: '-', x2: '-', x3: '-', x4: '-' });

            this.kk.length = 0
            this.kk.push({ x1: '-', x2: '-', x3: '-', x4: '-' });

            for (let x = 0; x < Number(this.truedata); x++) {
                this.calpk_(x)
                this.calkk(x)
                this.calpk(x)
            }
        },
        //先验估计
        xkhat_(x) {
            return this.mult(this.matrixA,this.xk[x])
        },
        //后验估计
        kxhat(x) {
            return this.matrixplus(this.xk_[x+1], this.mult(this.kk[x+1], this.matrixsubtract({ x1: this.calpositionarray[x], x3: this.calspeedarray[x] }, this.xk_[x+1])))
        },
        //先验后验全家桶
        yyyy() {
            this.xk.length = 0
            this.xk_.length=0
            this.xk.push({ x1: 0, x3: Number(this.v0) })
            this.xk_.push({ x1: '-', x3: '-' })
            for (let x = 0; x < Number(this.truedata); x++){
                this.xk_.push(this.rounnnd(this.xkhat_(x)))
                this.xk.push(this.rounnnd(this.kxhat(x)))
            }
        },
        //重新运行程序
        all() {
            //添加噪声
        var gaussian = require('gaussian');
        var distribution = gaussian(0, this.turepositiondeviationvalue);
        var dsi1 = gaussian(0, this.turespeeddeviationvalue)
        var dsi2 = gaussian(0, this.calspeeddeviationvalue)
        var dsi3 = gaussian(0, this.calpositiondeviationvalue)

        this.turepositiondeviation.length = 0
        this.turespeeddeviation.length = 0
        this.calpositiondeviation.length = 0
        this.calspeeddeviation.length = 0
        
        for (let x = 0; x < Number(this.truedata); x++) {
            this.calpositiondeviation.push(Math.round(dsi2.ppf(Math.random()) * 100) / 100)
            this.calspeeddeviation.push(Math.round(dsi3.ppf(Math.random()) * 100) / 100)
            }

        for (let x = 0; x < Number(this.truedata) + 1; x++) {
            this.turepositiondeviation.push(Math.round(distribution.ppf(Math.random()) * 100) / 100);
            this.turespeeddeviation.push(Math.round(dsi1.ppf(Math.random()) * 100) / 100)
        }
        /////////////

        //计算实际速度methods中
        this.calstruespeed()

        //计算实际位置methods中
        this.caltrueposition()

        //计算测量位置
        this.vposition()

        //计算测量位置
        this.vspeed()

        //定义A矩阵
        this.matrixA = { x1: 1, x2: this.time, x3: 0, x4: 1 }

        //定义B矩阵
        this.matrixB = { x1: Number(this.turepositiondeviationvalue), x2: Number(this.matrixB_con2), x3: Number(this.matrixB_con2), x4: Number(this.turespeeddeviationvalue) }

        //定义D矩阵
        this.matrixD = { x1: Number(this.calpositiondeviationvalue), x2: Number(this.matrixD_con1), x3: Number(this.matrixD_con1), x4: Number(this.calspeeddeviationvalue) }

        /////////////
        this.qjt()
        this.yyyy()

        //序号
        this.num.length = 0
        for (let x = 0; x < Number(this.truedata) * 2 + 1; x++) {
            if (x % 2 == 0) {
                this.num[x] = x / 2
                this.num[x + 1] = undefined
            }

        }







        //调整inputdiv高度统一 !!!!!!!!!!!!!!!!!!!!!!nextTick
        this.$nextTick(() => {
            let inputdivlist = document.querySelectorAll('.inputdiv')
            inputdivlist.forEach((item) => {
                let children = item.parentNode.childNodes
                item.style.height = item.parentNode.parentNode.childNodes[0].offsetHeight - children[0].offsetHeight + 'px'
            })
        })

        //调整序号高度统一
        function lisu(i) {
            const lisy = document.querySelectorAll(i)
            lisy.forEach((item) => {
                item.style.height = item.parentNode.childNodes[2].clientHeight + 1.6 + 'px';
            })
        }
        this.$nextTick(() => {
            lisu('.fuc')

        })
        }
    },
    beforeDestroy() {
        cancelAnimationFrame(this.observerp)
    }
    }
</script>

