<template>

        <div class="trans" :class="{'disabled_fliter':disabled}">
            <input type="checkbox" :id="label" v-model="state" style="display:none" @click.stop="1">
            <label :for="label" :class="{'disabled_color':disabled}">
                <span></span>
            </label>
        </div>

</template>

<script>

    export default {
        name: 'inputt',
        data() {
            return {
            }
        },
        // props:['label','setting_state','disabled'],
        props:{
            label:{
                type:String,
                required:true
            },
            setting_state:{
                type:String,
                default:"false"
            },
            disabled:{
                type:Boolean,
                required:false
            },
            stroage:{
                type:Boolean,
                default:true
            },
            value:{
                required:false
            }
        },
        computed:{
            state:{
                get(){
                    return JSON.parse(sessionStorage[this.label] || this.setting_state)
                },
                set(value){
                    if(this.disabled) alert('你干嘛？！')
                    else this.$emit('input',value)
                    if(this.stroage && !this.disabled) sessionStorage[this.label]=value
                }
            }
        },
        methods:{

        },
        watch:{

        }
    }
</script>

<style lang="" scoped>
    .trans{
        width: 60px;
        height: 25px;
        background-color: whitesmoke;
        border-radius: 15px;
        overflow: hidden;
    }
    .trans label{
        background-color: whitesmoke;
        width: 100%;
        height: 100%;
        display: inline-block;
        box-sizing: border-box;
        -webkit-transition: .2s;
        transition: .2s;
    }
    .trans label span{
        width: 20px;
        height: 20px;
        background-color: rgb(51, 161, 251);
        display: inline-block;
        border-radius: 50%;
        margin: 2.5px;
        -webkit-transition: .2s cubic-bezier(1,.99,.42,1.9);
        transition: .2s cubic-bezier(1,.99,.42,1.9);
        z-index: 3;
    }
    .trans input:checked + label{
        background-color: rgb(79, 231, 71);
        transition: .2s;
    }
    .trans input:checked + label span{
        background-color: whitesmoke;
        transition: .2s ;
        margin: 2.5px 2.5px 2.5px 37.5px;
        z-index: 3;
    }
    .disabled_fliter{
        filter: brightness(0.7);
        pointer-events: none;
    }
    .disabled_color{
        background-color: var(--red) !important;
        pointer-events: none !important;
    }
    .disabled_color span{
        background-color:whitesmoke !important;
    }
</style>


