import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import beziercurve from '@/components/list1/beziercurve'
import checkboxx from '@/components/list2/checkboxx'
import perlinnoise from '@/components/list1/perlinnoise'
import pnoise from '@/components/list3/pnoise'
import simplexnoise from '@/components/list1/simplexnoise'
import snoise from '@/components/list3/snoise'
import KalmanFiltering from '@/components/list1/KalmanFiltering'
import devips from '@/components/list1/DEVIPS'
import ev from '@/components/list1/ev'
import curves from '@/components/list1/curves'
import particular_b from '@/components/list1/particular_b'
Vue.use(Router)



export default new Router({
  routes: [
    {
      path:'/',
      redirect:'helloworld'
    },{
      path: '/helloworld',
      component: HelloWorld
    },{
      path:'/beziercurve',
      component:beziercurve
    },{
      path:'/checkboxx',
      component:checkboxx
    },{
      path:'/perlinnoise',
      component:perlinnoise
    }, {
      path:'/pnoise',
      component:pnoise
    }, {
      path: '/simplexnoise',
      component:simplexnoise
    }, {
      path: '/snoise',
      component:snoise
    }, {
      path: '/KalmanFiltering',
      component:KalmanFiltering
    }, {
      path: '/devips',
      component:devips
    }, {
      path: '/ev',
      component:ev
    }, {
      path: '/curves',
      component:curves
    }, {
      path: '/particular_b',
      component:particular_b
    }
  ]
})



