<template>
    <div class="numInput">
        <div class="numInput_body">
            <div class="numInput_up" @click="add">+</div>
            <div class="numview">
                <input type="number" v-model="newvalue">
            </div>
            <div class="numInput_down" @click="subtract">-</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'numInput',
        data() {
            return {
                newvalue:this.value,
            }
        },
        props:['max','min','value','step'],
        mounted(){
            
        },
        methods:{
            add(){
                if(this.newvalue<this.max)
                this.newvalue+=this.step
                this.$emit('input',this.newvalue)
            },
            subtract(){
                if(this.newvalue>this.min)
                this.newvalue-=this.step
                this.$emit('input',this.newvalue)
            },

        },
    watch: {
        value(newvalue) {
                this.newvalue = newvalue
            },
            newvalue(){
                this.newvalue=JSON.parse(this.newvalue)
                if(this.newvalue>this.max) this.newvalue=this.max
                if(this.newvalue<this.min) this.newvalue=this.min
                this.$emit('input',this.newvalue)
            }
        }
    }
</script>

<style scoped>

    .numInput_body{
        width: 100px;
        height: 30px;
        display: flex;
        border-radius: 5px;
        overflow: hidden;
    }
    .numInput_up,.numInput_down{
        width: 20px;
        height: 30px;
        background-color: var(--better-blue);
        font-weight: bold;
        display: flex;
        justify-content: center;
        align-items: center;
        color: var(--white);
        user-select: none;
        cursor: pointer !important;
    }
    input{
        height: 30px;
        width: 100%;
        text-align: center;
        border-radius:0 !important ;
        box-sizing: border-box;
        font-family: 'Roboto Mono','Avenir', Helvetica, Arial, sans-serif;
    }
    input:focus{
        border: 2px var(--better-blue) solid;
    }
    input::-webkit-inner-spin-button,input::-webkit-outer-spin-button,input{
        -webkit-appearance: none;
        appearance: none;
        outline: none;
        border: none;
    }
    .numview{
        width: 60px;
    }
</style>


