<template>
    <div class="checkboxx">
        <div id="checkboxx_body">
            <div id="checkboxx_content"><span style="color:var(--white);font-size:20px">删掉咯</span>
                <!-- <div id="hello_btn">
                    <div id="btn">
                        <div id="btn_array">
                            <h1>封装checkbox按钮</h1>
                            <input_scroll label="setting_1" setting_state="true"></input_scroll>
                        </div>
                    </div>
                    
                    
                </div>

                <div id="text_int">
                    <p>在做完网站的鼠标特效后发现有时候根本找不到鼠标在哪里，虽然也不是很常见的情况，但还是决定做一个控制样式的选项，顺便直接做成组件以后方便用</p>
                    <p>但是做完后发现一个很蠢的问题，就是如果刷新的话样式会便回默认值，所以还需要进行数据存储和判断</p>
                    <h2>基本框架</h2>
                    <p>首先做出组件的基本框架如下，并设置样式，通过组件标签上传入的“label”对input和label进行绑定</p>
                    <div id="opopo">
                        <input_scroll label="setting_p" id="rotate_input" setting_state="true"></input_scroll>
                    </div>
                    <div class="tons_code">
                        <pre v-highlight><code>
        &lt;div class="trans"&gt;
            &lt;input type="checkbox" v-id="label" style="display:none"&gt;
            &lt;label :for="label"&gt;
                &lt;span>&lt;/span&gt;
            &lt;/label&gt;
        &lt;/div&gt;
                        </code></pre>
                    </div>

                    <h2>初始化及获取状态</h2>
                    <p>在组件标签上传入setting_state="true"或"false"初始化开或关（props做一下严格的定义），input标签上使用v-model进行双向绑定，由于v-model中不能写表达式，所以使用计算属性对值进行判断，也可以用watch监听</p>
                    <p>为了保存状态使用sessionStorage方法暂存，点击按钮时state会取为按钮的状态，并触发set()函数存储该值</p>
                    <p>当获取state的值时，由sessionStorage[this.label] || this.setting_state，当未点击按钮时sessionStorage[this.label]为null，此时取后者值，有存储时取存储值，且二者值均为字符类型，使用JSON.parse()可转化为布尔值</p>
                    <div class="tons_code">
                        <pre v-highlight><code>
        &lt;div class="trans"&gt;
            &lt;input type="checkbox" v-id="label" style="display:none" v-model="state"&gt;
            &lt;label :for="label"&gt;
                &lt;span>&lt;/span&gt;
            &lt;/label&gt;
        &lt;/div&gt;

        export default {
        props:{
            label:{
                type:String,
                required:true
            },
            setting_state:{
                type:String,
                default:"false"
            }
        },
        computed:{
            state:{
                get(){
                    return JSON.parse(sessionStorage[this.label] || this.setting_state)
                },
                set(value){
                    sessionStorage[this.label]=value
                }
            }
        }
        }
                        </code></pre>
                    </div>

                    <h2>其它(无聊)功能</h2>
                    <p>虽然好像用不到，不过还是加了一个禁止改变的功能，组件标签加:disabled="something"，data中取值为true可以开启</p>
                    <p>再在组件内部设置动态属性，用pointer-event:none属性屏蔽鼠标事件</p>
                    <p>但是滑稽的是，属性在开发者模式中是可以改的，因此还要在代码层面进行监测，只需要在computed计算state的值时增加一个门槛if(this.disabled)，并警告不怀好意者alert('你干嘛？！')</p>
                    <div id="putput">
                        <input_scroll label="setting_l" setting_state="false" :disabled="disabledd" @click.native="(disableddd=!disableddd)&&(disabledddd=!disabledddd)"></input_scroll>
                        <input_scroll label="setting_ll" setting_state="false" :disabled="disableddd" @click.native="(disabledd=!disabledd)&&(disabledddd=!disabledddd)"></input_scroll>
                        <input_scroll label="setting_lll" setting_state="true" :disabled="disabledddd" @click.native="(disabledd=!disabledd)&&(disableddd=!disableddd)"></input_scroll>
                        </div>
                    <div class="tons_code">
                        <pre v-highlight><code>
        //父组件(highlight.js在下面的那句话g了，所以注释掉)
        //&lt;inputttttt label="xswl" setting_state="true" :disabled="disabled"&gt;&lt;/inputttttt&gt;

        //子组件
        &lt;div class="trans" :class="{'disabled_pointer':disabled}"&gt;
            &lt;input type="checkbox" :id="label" v-model="state" style="display:none"&gt;
            &lt;label :for="label" :class="{'disabled_color':disabled}"&gt;
                &lt;span&gt;&lt;/span&gt;
            &lt;/label&gt;
        &lt;/div&gt;

    export default {
        props:{
            label:{
                type:String,
                required:true
            },
            setting_state:{
                type:String,
                default:"false"
            },
            disabled:{
                type:Boolean,
                required:false
            }
        },
        computed:{
            state:{
                get(){
                    return JSON.parse(sessionStorage[this.label] || this.setting_state)
                },
                set(value){
                    if(this.disabled) alert('你干嘛？！')
                    else sessionStorage[this.label]=value
                }
            }
        }
    }
                        </code></pre>
                    </div>
                    <h2>坑坑坑</h2>
                    <p>当在组件标签上绑定@click事件时发现并没有生效，此时定义了x = true，而@click="x =! x"，但是改成@click = "false"却能正常运行，这说明x =! x被执行了偶数次，经过疯狂调试和网上搜索，发现input与label非常离谱，当点击label后会将焦点移动到input上，相当于又点击一次input，从而触发两次冒泡事件。只需要用stop修饰符阻止input的冒泡事件即可解决</p>
                    <div class="tons_code">
                        <pre v-highlight><code>
        &lt;input type="checkbox" :id="label" v-model="state" style="display:none" @click.stop="1"&gt;&lt;/input&gt;
                        </code></pre>
                    </div>
                    <h2>后续</h2>
                    <p>以上不难看出使用checkbox的麻烦，感觉可能以后用的时候还会出一些bug，所以还是用div直接做组件省事，最好还是直接用组件标签的v-model加this.$emit传递数据，这个已经在最新的组件用了。最后还加了一个是否保存选择的判断属性</p>
                </div> -->
            </div>
        </div>
    </div>
</template>

<script>
import { onMounted } from 'vue'

    export default {
        name: '',
        data() {
            return {
                disabledd:true,
                disableddd:false,
                disabledddd:false
            }
        },
        mounted(){
            // setInterval(()=>{
            //     document.getElementById('setting_p').checked=!document.getElementById('setting_p').checked
            // },2000)
        },methods:{

        }
    }
</script>

<style lang="" scoped>

    #checkboxx_body{
        height: fit-content;
        width: 100%;
        display: flex;
        justify-content: center;
    }

                /* 手机端 */

    @media screen and (max-device-width: 768px){
        #checkboxx_content{
        width: 900px;
        padding-top: 40px;
    }
    #hello_btn h1{

        font-size: 2.5em;
        letter-spacing: 5px;
    }
    }





    @media screen and (min-device-width: 768px) and (min-width:1200px) {

    #checkboxx_content{
        width: 900px;
        padding-top: 80px;
    }
            #hello_btn h1{

        font-size: 2.5em;
        letter-spacing: 5px;
    }

    }
    @media screen and (min-device-width: 768px) and (max-width:1200px) {

    #checkboxx_content{
        width: 900px;
        padding-top: 80px;
    }
            #hello_btn h1{

        font-size: 2.5em;
        letter-spacing: 5px;
    }

    }
    @media screen and (min-device-width: 768px) and (max-width:992px) {
    #checkboxx_content{
        width: 740px;
    }
        #hello_btn h1{

        font-size: 2.5em;
        letter-spacing: 5px;
    }
    }
    @media screen and (min-device-width: 768px) and (max-width:768px) {
    #checkboxx_content{
        width: 520px;
        max-width: 100vw;
    }
        #hello_btn h1{

        font-size: 2em;
        letter-spacing: 5px;
    }
    }


    #checkboxx_content{
        padding-right: 5px;
        padding-left: 5px;
    }
    #hello_btn{
        width: 100%;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        
    }
    #btn{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    #hello_btn h1{
        color: whitesmoke;
        display: inline-block;
        text-align: center;
        height: 90px;
        line-height: 90px;
        z-index: 2;
        margin-bottom: 20px;
    }
    #btn_array{
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

    }
    #btn_array .trans{
        position: absolute;
        transform: rotateZ(-135deg);
        top: 0;
        right: 0;
    }
    #checkboxx_content p{
        color: var(--white);
        font-weight: 600;
        letter-spacing: 1px;
        text-indent: 2em;
        margin: 40px 0;
        font-size: 1em;
    }


        .tons_code{
        width: 100%;
        box-sizing: border-box;
        font-size: 13px;
        margin: 30px 0;
        display: flex;
        overflow-y: overlay;
        background-color: #282C34;
        max-height: 600px;


}


    .tons_code::-webkit-scrollbar{
        width: 5px;
        height: 5px;
    }
    .tons_code::-webkit-scrollbar-corner{
        background-color: transparent;
    }
    .tons_code::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: #b6b6b6;
}


    
.tons_code *{
    font-family: 'Roboto Mono', monospace;
}
#rotate_input{
    animation: rrr 4s infinite linear;
    position: absolute;
    box-shadow: 0 0 55px 10px rgb(171, 171, 171);
}
@keyframes rrr{
    to{transform: rotateZ(1turn);}
}
#opopo{
    display:flex;
    justify-content:center;
    position: relative;
    align-items: center;
    padding: 30px;
}
#text_int h2{
    color: var(--light-green);
    margin-top: 70px;
}
#putput{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
</style>


