<template>
    <div id="curves_body">
        <div id="curves_content">
            <canvas id="curves_canvas"></canvas>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'curves',
        data() {
            return {
                frame: undefined,
                
            }
        },
    mounted() {
        const that = this
        const curves_content = document.getElementById('curves_content')
        const curves_canvas = document.getElementById("curves_canvas")
        curves_canvas.width = curves_content.clientWidth
        curves_canvas.height = curves_content.clientHeight
        const curves_ctx = curves_canvas.getContext('2d')

        curves_ctx.lineJoin = "round"
        curves_ctx.lineCap= "round"
        

        class lines{
            constructor(length,speed,width,color) {
                this.dots_array = [];
                this.xl = [];
                this.yl = [];
                this.length = length;
                this.width = width;
                this.color = color;
                this.speed = speed;
                this.count = 0;

                this.ran = () => {
                    const x = Math.random() * curves_canvas.clientWidth
                    const y = Math.random() * curves_canvas.clientHeight
                    
                    for(let xy = 0; xy<this.length; xy++) {
                        this.dots_array.push([x, y]);
                    }
                    for(let xy = 0; xy<40; xy++) {
                        this.xl.push(Math.random() * 20 - 10);
                        this.yl.push(Math.random() * 20 - 10);
                    }
                    this.xl.push(this.xl[0]);
                    this.yl.push(this.yl[0]);
                }
                this.ran()

                this.noise = (x, array) => {
                    x = x % (array.length - 1);
                    const z = x | 0;
                    const dx = x - z;
                    const dy = dx - 1;
                    //const t = 6 * dx * dx * dx * dx * dx - 15 * dx * dx * dx * dx + 10 * dx * dx * dx;
                    const t = 3 * dx * dx - 2 * dx * dx * dx;
                    const xx = dx * array[z];
                    const yy = dy * array[z + 1];
                    return xx + t * (yy - xx)
                }

                this.draw = () => {
                    curves_ctx.beginPath()
                    this.dots_array.forEach((item,index) => {
                        if (index == 0) {
                            curves_ctx.moveTo(item[0], item[1]);
                        } else if(index % 20 == 0) {
                            curves_ctx.lineTo(item[0], item[1]);
                        }
                        
                    })
                    
                    const lin = curves_ctx.createLinearGradient((this.dots_array[0][0]), this.dots_array[0][1], this.dots_array[this.dots_array.length - 1][0], this.dots_array[this.dots_array.length - 1][1]);
                    lin.addColorStop(0, 'hsla(' + this.color + ')');
                    lin.addColorStop(1, 'hsla(' + this.color + ',0)');
                    curves_ctx.strokeStyle = lin;
                    curves_ctx.lineWidth = this.width
                    curves_ctx.stroke()

                    this.dots_array.pop();
                    this.dots_array = [[this.dots_array[0][0] + this.noise(this.count,this.xl), this.dots_array[0][1] + this.noise(this.count,this.yl)], ...this.dots_array];
                    this.count += this.speed;

                    
                }
            
            }


            
        }

        let curves = [];
        let color_set = ['354, 88%, 71%','343, 40%, 59%','272, 15%, 42%','208, 40%, 35%'];
        for (let x = 0; x < 2000; x++){
            curves.push(new lines(Math.floor(Math.random()*500) + 100,Math.random()*0.002,Math.random()*3,color_set[Math.floor(Math.random()*color_set.length)]))
        }
        



        function move() {
            curves_ctx.clearRect(0, 0, curves_canvas.clientWidth, curves_canvas.clientHeight)
            for (let item of curves) {
                item.draw()
            }
            that.frame = window.requestAnimationFrame(move)
        }
        move()
        

        


    },
    beforeDestroy() {
        window.cancelAnimationFrame(this.frame)
    }
    }
</script>

<style scoped>
    #curves_content{
        height: 100vh;
        overflow: hidden;
    }
</style>


