<template>
    <div class="devips_body">
        <div id="devips_content">
            <div id="mapco">
                <canvas id="layer"></canvas>
                <canvas id="map" style="pointer-events:none"></canvas>
            </div>
            <div id="contrmap">
                <div id="cl">
                    
