68.0739], [524.23552, 256.31809], [861.76534, 855.36444], [99.137446, 337.44049], [470.34811, 688.96363], [453.21175, 357.33687], [970.30001, 473.46597], [318.47672, 617.2167], [51.459485, 690.86458], [228.43103, 362.67138], [105.04268, 832.02461], [626.78015, 578.85777], [898.06909, 995.79349], [764.34883, 268.9948], [29.413063, 881.4087], [226.34196, 562.52212], [111.32532, 436.70829], [396.974, 283.81945]],
                iotData1: [175984580, 239658110, 39919296, 163433520, 660657690, 937880220, 286306610, 782897300, 280552860, 733328440, 951902040, 322432370, 37957922, 409660730, 454281840, 702243280, 312260500, 545552510, 759680730, 941764830, 517677580, 236788570, 699569700, 799676270, 411986600, 509656200, 961492950, 42809817, 419603980, 785522210, 904097720, 551578460, 226845350, 845949020, 965679510, 629431500, 938230400, 710176580, 752513090, 324120870, 641063880, 736629080, 413790560, 797891510, 433869920, 781738160, 646537850, 386816610, 723314070, 434909640, 503164840, 192357100, 40943917, 93775597, 561518590, 433897880, 259226090, 892197430, 652043550, 508083160, 151305150, 693699900, 936256420, 872516500, 175984580, 239658110, 39919296, 163433520, 660657690, 937880220, 286306610, 782897300, 280552860, 733328440, 951902040, 322432370, 37957922, 409660730, 454281840, 702243280, 312260500, 545552510, 759680730, 941764830, 517677580, 236788570, 699569700, 799676270, 411986600, 509656200, 961492950, 42809817, 419603980, 785522210, 904097720, 551578460, 226845350, 845949020, 965679510, 629431500],
                pushresult: [],//记录每次优化结果的种群的所有个体用于第二模块,
                yhcs: 0,//第二模块统计优化次数
                val: [],//统计权重数组
                val_mean: 0,//val的均值
                val_sum: 0,//val的总数
                energy_total: 0,//所有优化能耗和
                energy_ind: [],//记录每次优化每个设备所在种群对应能耗
                energy_array: [],//每一次优化的能耗
                deeeeeevip: undefined,//二模块大于该值时信息显示
                amount_ratio: [],//数量占比
                energy_ratio: [],//能耗占比
                visiable: 1,//加载中
                step_forward: 10,//动画刷新速度
                
                
                
            }
    }, mounted() {

            
        
            const map = document.getElementById('map')
            map.height = map.width = 601
            const ratio = map.width / 1000;
            const layer = document.getElementById('layer')

            const mapctx = map.getContext('2d')
            const layerctx = layer.getContext('2d')
            mapctx.fillStyle = 'rgb(91, 212, 176)'
            mapctx.strokeStyle = 'rgb(68, 115, 255,0.5)'
            mapctx.lineWidth = 1

