            layer.height = 703

            const t = this

            const sel = document.getElementById('sel');
            sel.addEventListener("change", () => {
                this.drawleft(mapctx, map, ratio)
            })

            const s1 = '设备位置'
            const s2 = '优化无人机位置'
            const s3 = '初始无人机位置'
            layerctx.font = '16px Roboto Mono'
            layerctx.textAlign = 'right bottom'
            const sl1 = layerctx.measureText(s1).width
            const sl2 = layerctx.measureText(s2).width
            const sl3 = layerctx.measureText(s3).width

            layer.addEventListener('click', (e) => {
                const x = e.offsetX
                const y = e.offsetY
                if (x > 90 && x < 110 + sl1 && y > layer.height - 36 && y < layer.height - 16) {
                    this.touch[0] = !this.touch[0]
                    this.drawleft(mapctx,map,ratio)
                }
                if (x > 100 + sl1 +40 && x < 100 + sl1 +60 + sl2 && y > layer.height - 36 && y < layer.height - 16) {
                    this.touch[1] = !this.touch[1]
                    this.drawleft(mapctx,map,ratio)
                }
                if (x > 100 + sl1 + sl2 +90 && x < 100 + sl1 + sl2 +110 + sl3 && y > layer.height - 36 && y < layer.height - 16) {
                    this.touch[2] = !this.touch[2]
                    this.drawleft(mapctx,map,ratio)
                }
            })

            let xxx = 0
            let yyy = 0
            let layerstate = false
            layer.addEventListener('mouseenter', () => {
                layerstate = true
            })
            layer.addEventListener('mousemove', (e) => {
                xxx = e.offsetX
                yyy = e.offsetY - 51
            })
            layer.addEventListener('mouseout', () => {
                layerstate = false
            })

            let one = 1
            let dsilist = []
            
            this.drawleft(mapctx,map,ratio)

            function drawLayer() {
                t.listener = window.requestAnimationFrame(drawLayer)
                layerctx.clearRect(0, 0, layer.width, layer.height)

                layerctx.fillStyle = 'rgb(91, 212, 176)'
                layerctx.fillText(s1, 100, layer.height - 20)
                layerctx.fillStyle = '#f2ac20'
                layerctx.fillText(s2, 100 + sl1 +50, layer.height - 20)
                layerctx.fillStyle = '#ea481f'
                layerctx.fillText(s3, 100 + sl1 + sl2 + 100, layer.height - 20)



                if (!t.touch[0]) {
                    layerctx.strokeStyle = 'rgb(91, 212, 176)'
                    layerctx.beginPath()
                    layerctx.moveTo(90, layer.height - 28)
                    layerctx.lineTo(110 + sl1, layer.height - 28)
                    layerctx.stroke()
                }

                if (!t.touch[1]) {
                    layerctx.strokeStyle = '#f2ac20'
                    layerctx.beginPath()
                    layerctx.moveTo(100 + sl1 +40, layer.height - 28)
                    layerctx.lineTo(100 + sl1 +60 + sl2, layer.height - 28)
                    layerctx.stroke()
                }

                if (!t.touch[2]) {
                    layerctx.strokeStyle = '#ea481f'
                    layerctx.beginPath()
                    layerctx.moveTo(100 + sl1 + sl2 +90, layer.height - 28)
                    layerctx.lineTo(100 + sl1 + sl2 +110 + sl3, layer.height - 28)
                    layerctx.stroke()
                }
            }
            drawLayer()

            const finaldone = document.getElementById('finaldone')
            const fictx = finaldone.getContext('2d')

            //开始优化按钮
            document.getElementById('start').addEventListener('click', () => {
                


                window.cancelAnimationFrame(t.listener)
                layer.style.pointerEvents = 'none'
                let x = document.createElement('div')
                // let txt = document.createTextNode("加载中")
                map.style.setProperty('z-index', 100000)
                finaldone.style.setProperty('z-index', 100000)
                x.id = 'jzz'
                x.style.zIndex = '99999'
                document.body.appendChild(x)
                this.visiable = 0;
                setTimeout(() => {
                    window.cancelAnimationFrame(t.observerp)
                    t.which = 0
                    t.evaluation = 0
                    t.FF = []
                    t.yh = []
                    t.cz = []
                    mm()
                    
                    
                    this.drawleft(mapctx,map,ratio)
                }, 1000)
                
                
                
                t.$nextTick(() => {
                    t.listener = window.requestAnimationFrame(drawLayer)
                })
                
            })

            
            function mm() {


                const that = t
                class parameter{
                constructor(xmin, xmax, ymin, ymax, rho, B, p, sigma, ph, D, F) {
                    this.xmin = xmin
                    this.xmax = xmax
                    this.ymin = ymin
                    this.ymax = ymax
                    this.rho = rho
                    this.B = B
                    this.p = p
                    this.sigma = sigma
                    this.ph = ph
                    this.D = D
                    this.F = F
                }
                }
            const para = new parameter(0, 1000, 0, 1000, 1e-6, 1e+6, 0.1, 1e-28, 1000, that.iotData1, 0.6);


            that.ini(para, mapctx, ratio, map, 1)
            // that.k1 = that.result.length
            mapctx.strokeStyle = '#f2ac20'




            

        
        let statec = true //使点过近时悬停效果只能发生一次
        let liszone//记录鼠标进入区域，当离开时可触发
        
        //监听鼠标悬停画布
        let xx
        let yy
        let io = false//鼠标是否在画布上
        let stt = true

        let colomn //使整列产生效果
        let movetarget  //移动渐变参数
        function r() {
            stt = true
            io = true
        }
            
        function rr(e) {
            xx = e.offsetX
            yy = e.offsetY

            if (Math.abs(xx - liszone) > 3 ) {
                //当鼠标离开点区域时重置效果
                statec = true
            }
        }

        function rrr() {
            io = false
            stt = false
            movetarget = 1300
        }






        finaldone.addEventListener('mouseenter', r)
        finaldone.addEventListener('mousemove', rr)
        finaldone.addEventListener('mouseout', rrr)
        

        

        
        function can(x, y, index) {
            if (Math.abs(xx - x) <= 3 && io && statec) {
                movetarget = x
                that.which = index
                statec = false
                liszone = x
            }
        }


        

            function d() {
            fictx.clearRect(0, 0, finaldone.width, finaldone.height)


                
            

            
            if (io) {
                fictx.beginPath()
                fictx.lineWidth = 1
                fictx.setLineDash([10,10])
                fictx.strokeStyle = 'rgb(255,255,255,0.3)'
                fictx.moveTo(movetarget, 30)
                fictx.lineTo(movetarget, finaldone.height - 20)
                fictx.stroke()
                fictx.closePath()
                fictx.setLineDash([1,0])
            }

            const zonex = finaldone.width - 40
            const zoney = finaldone.height - 50

            const xgap = zonex / that.FF.length

            const ctxratio = zoney / (that.FF[0] - that.FF[that.FF.length - 1])

            let dotbe = zoney + that.FF[that.FF.length - 1] * ctxratio
            

            fictx.lineCap = 'round'
            fictx.lineJoin = 'round'

            fictx.beginPath()
            fictx.font = '10px Roboto Mono'
            fictx.strokeStyle = 'rgb(255,255,255,0.1)'
            fictx.fillStyle = 'whitesmoke'
            fictx.fillText(0, 5, dotbe + 10)
            fictx.moveTo(20, dotbe + 30)
            fictx.lineTo(finaldone.width - 10, dotbe + 30)
            fictx.stroke()

            let count = 0
            for (let y = dotbe + 30 - 100; y >= 30; y -= 100){
                count++
                fictx.beginPath()
                fictx.textAlign = 'left'
                fictx.lineWidth = 1
                fictx.fillText((100 * count/ctxratio).toFixed(1), 5, y - 20)
                fictx.moveTo(20, y)
                fictx.lineTo(finaldone.width - 10, y)
                fictx.stroke()
            }

            

            
            for (let index = 0; index < that.FF.length; index++){
                fictx.textAlign = 'center'
                fictx.fillStyle = 'rgb(255,255,255,0.9)'
                fictx.font = Math.max(10 - (that.truedata / 60 | 0) * 2, 1) + 'px Roboto Mono'
                fictx.fillText('.', 30 + xgap * index, finaldone.height - 10)
                fictx.fillStyle = 'rgb(68, 115, 255)'
            }fictx.lineWidth = 2

            

            

            fictx.beginPath()
                fictx.strokeStyle = 'rgb(68, 115, 255)'
                
                that.FF.forEach((item, index) => {
                    const x = 30 + xgap * index
                    const y = dotbe + 30 - item * ctxratio

                    
                    fictx.lineTo(x, y)
                    can(x,y,index)
                    


                })
                fictx.stroke()

                if (io) {
                fictx.fillStyle = 'red'
                const index = Math.round((colomn - 30)/xgap)
                let left_top_x = movetarget + 20
                let left_top_y = yy + 20
                fictx.font = '16px Roboto Mono'

                const num = that.which
                const s1 = '能耗:'+that.FF[num]
                const s2 = '优化次数：'+(num+1)
                const s3 = '能耗优化值:'+-1*that.yh[num]
                const s4 = '优化操作：'+that.cz[num]

                
                const width = Math.max.apply(null, [fictx.measureText(s1).width, fictx.measureText(s2).width, fictx.measureText(s3).width, fictx.measureText(s4).width])
                fictx.fillStyle = 'rgb(255,255,255)'
                if (left_top_x + width + 40 >= finaldone.width) {
                    left_top_x -= (width + 60)
                }
                if (left_top_y + 150 >= finaldone.height) {
                    left_top_y -= 190
                }
                fictx.fillRect(left_top_x, left_top_y, width + 20, 90)
                fictx.textAlign = 'left'
                fictx.fillStyle = 'rgb(35,35,35)'
                fictx.fillText(s1, left_top_x + 10, left_top_y + 20)
                fictx.fillText(s2, left_top_x + 10, left_top_y + 40)
                fictx.fillText(s3, left_top_x + 10, left_top_y + 60)
                fictx.fillText(s4, left_top_x + 10, left_top_y + 80)
                
            }
            }


            function draw() {
                that.observerp = window.requestAnimationFrame(draw)
                d()
            }
            draw()

            
            }



            //第二板块图下板块//////////////////////////////////////////////////////////////////////
            this.iotPosition.forEach(() => {
                this.energy_ind.push(0)
            })

            const de_mul = document.getElementById('de_mul')
            const de_mul_ctx = de_mul.getContext('2d')
            de_mul.height = de_mul.width = 601
            const ratio_mul = de_mul.width / 1000;
            de_mul_ctx.clearRect(0, 0, de_mul.width, de_mul.height)

            //网格
            de_mul_ctx.strokeStyle = 'rgb(68, 115, 255,0.5)'
            for (let x = 0; x < de_mul.height / 100; x++){
                de_mul_ctx.beginPath()
                de_mul_ctx.moveTo(x * 100, 0)
                de_mul_ctx.lineTo(x * 100, de_mul.height)
                de_mul_ctx.moveTo(0,x * 100)
                de_mul_ctx.lineTo(de_mul.height,x * 100)
                de_mul_ctx.stroke()
            }

            //第二板块图上板块
            const above_mul = document.getElementById('above_mul')
            const above_mul_ctx = above_mul.getContext('2d')
            above_mul.height = 641
            above_mul.width = 1202
            const ratio_above_mul_height = de_mul.height / 1000 //缩放比
            const offset_x = (above_mul.width - de_mul.width) / 2

            let above_mul_x = 0
            let above_mul_y = 0
            above_mul.addEventListener('mousemove', (e) => {
                above_mul_x = e.offsetX
                above_mul_y = e.offsetY
            })

            function info_stste(xxx, yyy, p, onx, uiu, iri, opo, index, opacity, paraer) {//控制二模块信息展示
                //控制颜色和显示
                if (xxx * xxx + yyy * yyy <= (t.val[index] + 2) * (t.val[index] + 2)) {
                    above_mul_ctx.fillStyle = 'rgb(234, 72, 31)'
                    above_mul_ctx.beginPath()
                    above_mul_ctx.arc(onx, uiu, 6, 0, Math.PI * 2)
                    above_mul_ctx.fill()
                    above_mul_ctx.strokeStyle = 'rgb(255,255,255)'
                    above_mul_ctx.fillStyle = 'rgb(255,255,255)'
                } else {
                    above_mul_ctx.strokeStyle = 'rgb(255,255,255,'+ opacity +')'
                    above_mul_ctx.fillStyle = 'rgb(255,255,255,'+ opacity +')'
                }

                if (onx < p && t.val_mean && t.val[index] / t.val_sum >= paraer) {          //若点在水平中心左边则向左边画
                    above_mul_ctx.beginPath()
                    above_mul_ctx.moveTo(onx, uiu);
                    above_mul_ctx.lineTo(onx - 20, uiu + 20)
                    above_mul_ctx.lineTo(Math.max(onx - 290,100), uiu + 20)
                    above_mul_ctx.stroke()
                    above_mul_ctx.fillText('数量占比:'+iri+'%', Math.max(0, Math.max(onx - 320, 100) - 70), uiu + 10)
                    above_mul_ctx.fillText('能耗占比:'+opo+'%', Math.max(0, Math.max(onx - 320, 100) - 70), uiu + 25)
                } else if (onx >= p && t.val_mean && t.val[index] / t.val_sum >= paraer) {        //若点在水平中心右边则向右边画
                    above_mul_ctx.beginPath()
                    above_mul_ctx.moveTo(onx, uiu);
                    above_mul_ctx.lineTo(onx + 20, uiu + 20)
                    above_mul_ctx.lineTo(Math.min(onx + 290,above_mul.width - 100), uiu + 20)
                    above_mul_ctx.stroke()
                    above_mul_ctx.fillText('数量占比:' + iri + '%', Math.min(onx + 320, above_mul.width - 100), uiu + 10)
                    above_mul_ctx.fillText('能耗占比:'+opo+'%', Math.min(onx + 320, above_mul.width - 100), uiu + 25)
                }
            }

            function above_mul_ctx_start() {
                t.observerpp = window.requestAnimationFrame(above_mul_ctx_start)
                above_mul_ctx.clearRect(0, 0, above_mul.width, above_mul.height);
                if (t.result) {
                    const p = above_mul.width / 2
                    t.iotPosition.forEach((item, index) => {
                        const onx = item[0] * ratio_above_mul_height + offset_x;
                        const xxx = above_mul_x - onx
                        const yyy = above_mul_y - item[1] * ratio_above_mul_height
