        draw_de_chart() {
            const chart_de_first = document.getElementById('chart_de_first')
            const chart_de_first_ctx = chart_de_first.getContext('2d')

            class lineChart {  /////////////////////////////////////////////////////////////////////////////////////////折线图
                constructor(array, color, canvas, ctx) {
                    this.array = array
                    this.color = color
                    this.canvas = canvas
                    this.ctx = ctx

                    this.width = this.canvas.width                       //传入的画布宽
                    this.height = this.canvas.height                       //传入的画布高
                    this.blank_above = 50                                     //画布上方留白
                    this.blank_below = 50                                     //画布下方留白
                    this.blank_width = 60                                       //画布左右各留白
                    this.height_true = this.height - this.blank_above - this.blank_below   //实际画布高

                    this.ctx.clearRect(0, 0, this.width, this.height)
                    this.ctx.lineJoin = "round"
                    this.ctx.lineWidth = 2
                    this.ctx.lineCap= "round"
                    this.array.forEach((i ,dex) => {

                        this.max = Math.max.apply(null, i)
                        this.min = Math.min.apply(null, i)
                        this.difference = this.max - this.min
                        this.height_ratio = this.height_true / this.difference   //纵向缩放比

                        this.ctx.beginPath()
                        this.ctx.strokeStyle = this.color[dex]
                        i.forEach((item, index) => {
                            const num = Number(item)
                            if (index != 0) {
                                this.ctx.lineTo(this.blank_width + (this.width - 2 * this.blank_width) / (i.length - 1) * index, this.height_true - num * this.height_ratio + this.blank_above)
                            } else {
                                this.ctx.moveTo(this.blank_width,this.height_true - num * this.height_ratio + this.blank_above)
                            }
                        })
                        this.ctx.stroke()
                    })
                    
                }
            }



