        draw_de_mul() {

            //第二板块
            const de_mul = document.getElementById('de_mul')
            const de_mul_ctx = de_mul.getContext('2d')
            de_mul.height = de_mul.width = 601
            const ratio_mul = de_mul.width / 1000;
            de_mul_ctx.clearRect(0, 0, de_mul.width, de_mul.height)

            //网格
            de_mul_ctx.strokeStyle = 'rgb(68, 115, 255,0.5)'
            for (let x = 0; x < de_mul.height / 100; x++){
                de_mul_ctx.beginPath()
                de_mul_ctx.moveTo(x * 100, 0)
                de_mul_ctx.lineTo(x * 100, de_mul.height)
                de_mul_ctx.moveTo(0,x * 100)
                de_mul_ctx.lineTo(de_mul.height,x * 100)
                de_mul_ctx.stroke()
            }

            if (this.pushresult) {
                this.val = []
                this.energy_ind = []
                this.iotPosition.forEach(() => {
                    this.val.push(0)
                    this.energy_ind.push(0)
                })
                this.pushresult.forEach((item, index) => {//[]指第一层
                    const ener = this.energy_array[index]
                    for (let xo = 0; xo < item.length; xo++){
                        //遍历权重
                        const x = item[xo][0]//指[[[]]]第三层
                        const y = item[xo][1]
                        this.iotPosition.forEach((itt,inn) => {
                            const xx = itt[0] - x
                            const yy = itt[1] - y
                            const distance = Math.sqrt(xx * xx + yy * yy)
                            if (distance <= 4) {                                        //判断多远算在某设备统计范围
                                this.val[inn] += 1
                                this.energy_ind[inn] += ener
                            }
                        })
                    }
                    this.yhcs = index + 1
                })
                //绘制权重圆
                de_mul_ctx.fillStyle = 'rgb(62, 192, 153,0.5)'
                this.iotPosition.forEach((item, index) => {
                    de_mul_ctx.beginPath()
                    de_mul_ctx.arc(item[0] * ratio_mul, item[1] * ratio_mul, this.val[index] + 2, 0, 2 * Math.PI);
                    de_mul_ctx.fill()
                })
                //绘制点
                this.pushresult.forEach((item, index) => {
                    de_mul_ctx.strokeStyle = 'rgb(242, ' + (index + 1) / this.pushresult.length *255 + ', ' + (index + 1) / this.pushresult.length *255 + ')'
                    for (let xo = 0; xo < item.length; xo++){
                        de_mul_ctx.beginPath()
                        de_mul_ctx.arc(item[xo][0] * ratio_mul, item[xo][1] * ratio_mul, 4, 0, 2 * Math.PI);
                        de_mul_ctx.stroke()
                    }
                })
                //计算val的平均值
                let mean = 0
                this.val.forEach((item) => {
                    mean += item
                })
                this.val_mean = mean / this.val.length
                this.val_sum = mean
            }
            },
        fitness(uav, iot, para) {
            let distance_all = []
            let mindistance = []
            for (let y = 0; y < iot.length; y++) {
                distance_all.push([])
            }
            for (let x = 0; x < iot.length; x++) {
                for (let y = 0; y < uav.length; y++) {
                    const xx = uav[y][0] - iot[x][0]
                    const yy = uav[y][1] - iot[x][1]
                    distance_all[x].push(xx * xx + yy * yy)
                }
            }
            distance_all.forEach((item) => {
                const d = Math.min.apply(null, item)
                item.forEach((item, index) => {
                    if (item - d == 0) {
                        mindistance.push(index)
                    }
                })
            })

            for (let x = 0; x < uav.length; x++) {
                let count = 0
                mindistance.forEach((item) => {
                    if (item == x) {
                        count++
                    }
                })
                if (count > this.max_connection) {
                    return [1, Infinity]
                }
            }

            let uav_timegroup = []
            let iot_timeas = 0
            let uav_timeas = 0

            for (let x = 0; x < uav.length; x++) {
                uav_timegroup.push([])
            }
            for (let x = 0; x < iot.length; x++) {
                const di = distance_all[x][mindistance[x]]
                const h = para.rho / di
                const r = para.B * Math.log2(1 + para.p * h / para.sigma)
                uav_timegroup[mindistance[x]].push(para.D[x] / r)
                iot_timeas += para.p * para.D[x] / r
            }

            for (let x = 0; x < uav_timegroup.length; x++) {
                const i = Math.max.apply(null, uav_timegroup[x])
                if (i != -Infinity) {
                    uav_timeas += para.ph * i
                }
            }

            return [0, 10000 * iot_timeas + uav_timeas]
        },
        smdx(para) {
            let only = 1
            let generate_F = 0;
            let generate = []
            while (only) {
                generate = []
                for (let x = 0; x < this.uav_amount; x++){
                    generate.push([para.xmin + (para.xmax - para.xmin) * Math.random(), para.ymin + (para.ymax - para.ymin) * Math.random()])
                }
                const c = this.fitness(generate, this.iotPosition, para)
                only = c[0]
                generate_F = c[1]
                this.k2 = generate_F.toFixed(0)
            }
            this.fl = generate
            return [generate,generate_F]
        },
        ini(para, mapctx, ratio, map, performation, gen, genF) {

            let generate = gen;
            let generate_F = genF;

            if (performation === 1) {
                const op = this.smdx(para);
                generate = op[0]
                generate_F = op[1]
                performation++
            }
            

            


            const n = this.opera(generate, para)
            for (let run = 0; run < this.step_forward; run++){
                for (let x = 0; x < n.length; x++){
                    //插入
                    const ins = generate.concat([n[x]])
                    const insF = this.fitness(ins, this.iotPosition, para)[1]

                    //移除
                    const [...rem] = generate
                    rem.splice(Math.floor(Math.random() * rem.length), 1)
                    const remF = this.fitness(rem, this.iotPosition, para)[1]

                    //替换
                    const [...rep] = generate
                    rep[Math.floor(Math.random() * rem.length)] = n[x]
                    const repF = this.fitness(rep, this.iotPosition, para)[1]

                    //随机上述两种操作
                    // const [...ran] = generate
                    // for (let x = 0; x < 4; x++){
                    //     const rnadnum = Math.floor(Math.random() * 3)
                    //     switch (rnadnum) {
                    //         case 0:
                    //             ran.push(n[x])
                    //             break
                    //         case 1:
                    //             ran.splice(Math.floor(Math.random() * ran.length), 1)
                    //             break
                    //         case 2:
                    //             ran[Math.floor(Math.random() * ran.length)] = n[x]
                    //             break
                    //     }
                    // }
                    //const ranF = this.fitness(ran, this.iotPosition, para)[1]

                    const a = insF - generate_F
                    const b = remF - generate_F
                    const c = repF - generate_F
                    //const d = ranF - generate_F
                    const mini = Math.min(a, b, c)//d)

                    if (mini < 0) {
                        this.yh.push(mini)
                        if (a == mini) {
                            generate = ins
                            generate_F = insF
                            this.FF.push(generate_F)
                            this.cz.push('插入')
                        } else if (b == mini) {
                            generate = rem 
                            generate_F = remF
                            this.FF.push(generate_F)
                            this.cz.push('移除')
                        } else if(c==mini) {
                            generate = rep
                            generate_F = repF
                            this.FF.push(generate_F)
                            this.cz.push('替换')
                        } //else if(d==mini) {
                        //     generate = ran
                        //     generate_F = ranF
                        //     this.FF.push(generate_F)
                        //     this.cz.push('2')
                        // }
                    } else if (b == 0) {
                        generate = rem 
                        generate_F = remF
                        this.yh.push(mini)
                        this.FF.push(generate_F)
                        this.cz.push('移除')
                    }

                    

                    this.evaluation += 3

                    if (this.evaluation % 11130 == 0) {
                        this.result = generate
                        this.k1 = generate.length
                        
                        this.drawleft(mapctx, map, ratio)
                        
                            
                        
                        
                    }
                    


                

                
                
                }
            }

                


            if (this.evaluation < this.max_evaluation){
                setTimeout(() => {
                    this.ini(para, mapctx, ratio, map, performation, generate, generate_F)
                },1)
            } else {
                this.k3 = generate_F.toFixed(0)
                this.energy_array.push(Number(this.k3))
                this.visiable = 1
                
                this.$nextTick(() => {
                    this.pushresult.push(generate)

                    this.amount_ratio = []
                    this.energy_ratio = []
                    this.draw_de_mul()
                    this.iotPosition.forEach((item, index) => {
                        this.amount_ratio.push(Number((this.val[index] / this.val_sum * 100).toFixed(1)))
                        this.energy_ratio.push(Number((this.energy_ind[index] / this.energy_total * 100).toFixed(3)))
                        
                    })
                })

                return [generate_F, generate]
            }
            
            
        },
        opera(subpop, para) {
            if (subpop.length <= 3) return subpop
            let array = []
            for(let x=0;x<subpop.length;x++){

                let rm = []
                for(let x=0;x<subpop.length;x++){
                    rm.push(x)
                }

                ////////////////////////////////////////////////////////////////////////////////////交叉策略1
                let three = []
                let a = Math.floor(Math.random() * subpop.length)
                three.push(a)
                rm.splice(a, 1)

                a = Math.floor(Math.random() * (subpop.length - 1))
                three.push(rm[a])
                rm.splice(a, 1)

                a = Math.floor(Math.random() * (subpop.length - 2))
                three.push(rm[a])

                let V = []
                V[0] = subpop[three[0]][0] + para.F*(subpop[three[1]][0]-subpop[three[2]][0])
                V[1] = subpop[three[0]][1] + para.F*(subpop[three[1]][1]-subpop[three[2]][1])
                
                V[0] = Math.max(para.xmin,Math.min(V[0], para.xmax))
                V[1] = Math.max(para.ymin, Math.min(V[1], para.ymax))
                //////////////////////////////////////////////////////////////////////////////////////


                // //////////////////////////////////////////////////////////////////////////////////////交叉策略2
                // let three = []
                // let a = Math.floor(Math.random() * subpop.length)
                // three.push(a)
                // rm.splice(a, 1)

                // a = Math.floor(Math.random() * (subpop.length - 1))
                // three.push(rm[a])
                // rm.splice(a, 1)

                // a = Math.floor(Math.random() * (subpop.length - 2))
                // three.push(rm[a])
                // rm.splice(a, 1)

                // a = Math.floor(Math.random() * (subpop.length - 3))
                // three.push(rm[a])
                // rm.splice(a, 1)

                // a = Math.floor(Math.random() * (subpop.length - 4))
                // three.push(rm[a])

                // let V = []
                // V[0] = subpop[three[0]][0] + para.F*(subpop[three[1]][0]-subpop[three[2]][0] + para.F*(subpop[three[3]][0]-subpop[three[4]][0]))
                // V[1] = subpop[three[0]][1] + para.F*(subpop[three[1]][1]-subpop[three[2]][1] + para.F*(subpop[three[3]][1]-subpop[three[4]][1]))
                
                // V[0] = Math.max(para.xmin,Math.min(V[0], para.xmax))
                // V[1] = Math.max(para.ymin, Math.min(V[1], para.ymax))
                // ////////////////////////////////////////////////////////////////////////////////////////
                
                let fate = []
                for (let x = 0; x < 2; x++){
                    if (Math.random() < 0.5) {
                        fate.push(1)
                    } else {
                        fate.push(0)
                    }
                    
                }
                fate[Math.floor(Math.random() * 2)] = 1
                
                array.push([V[0] * fate[0] + subpop[x][0] * (1 - fate[0]), V[1] * fate[1] + subpop[x][1] * (1 - fate[1])])
            }
            
            return array
        },
        drawleft(mapctx, map, ratio) {

            
                
                mapctx.clearRect(0, 0, map.width, map.height)
                


                mapctx.strokeStyle = 'rgb(68, 115, 255,0.5)'
                for (let x = 0; x < map.height / 100; x++){
                    mapctx.beginPath()
                    mapctx.moveTo(x * 100, 0)
                    mapctx.lineTo(x * 100, map.height)
                    mapctx.moveTo(0,x * 100)
                    mapctx.lineTo(map.height,x * 100)
                    mapctx.stroke()
                }

                if (this.touch[0]) {
                    mapctx.fillStyle = 'rgb(91, 212, 176)'
                    for (let x = 0; x < this.iotPosition.length; x++) {
                        mapctx.beginPath()
                        mapctx.arc(this.iotPosition[x][0] * ratio, this.iotPosition[x][1] * ratio, 2, 0, 2 * Math.PI);
                        mapctx.fill()
                    }
                }

            if (this.result != undefined && this.touch[1]) {
                
                    mapctx.strokeStyle = '#f2ac20'
                    for (let x = 0; x < this.result.length; x++){
                        mapctx.beginPath()
                        mapctx.arc(this.result[x][0] * ratio, this.result[x][1] * ratio, 4, 0, 2 * Math.PI);
                        mapctx.stroke()
                    }
                }

                if (this.fl != null && this.touch[2]) {
                    mapctx.fillStyle = '#ea481f'
                    for (let x = 0; x < this.fl.length; x++) {
                        mapctx.beginPath()
                        mapctx.arc(this.fl[x][0] * ratio, this.fl[x][1] * ratio, 2, 0, 2 * Math.PI);
                        mapctx.fill()
                    }
                }
                
            }
    }, beforeDestroy() {
        window.cancelAnimationFrame(this.observerp)
        window.cancelAnimationFrame(this.listener)
        window.cancelAnimationFrame(this.observerpp)
    }
    }
</script>

<style scoped>
    #devips_content{
        margin: 20px 10px;
        height: fit-content;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    #expend_devip{
        display: flex;
        justify-content: center;
        margin-bottom: 50px;
        position: relative;
    }
    #devips_content>div{
        min-width: 620px;
        height: 700px;
    }
    #mapco{
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }
    #mapco canvas{
        position: absolute;
    }
    #start{
        width: 100px;
        height: 30px;
        right: 50px;
        top: 50px;
        font-weight: bold;
    }
    #cl{
        flex-direction:row !important;
        justify-content: space-evenly;
        width: 700px;
        
    }
    #sbdx{
        flex-direction:column;
        justify-content: space-around;
    }
    #sbdx>div{
        display: flex;
        
    }
    #resultdiv{
        width: 200px;
        height: 110px;
        right: 190px;
        top: 0px;
        flex-direction: column;
        justify-content: space-around;
    }
    #resultdiv > div{
        padding: 4px 0;
    }
    #contrmap{
        background-color: rgb(47, 50, 63);
        margin-right: 10px;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        padding: 10px 20px;
        justify-content: space-around;
        box-sizing: border-box;
        align-items: center;
        max-width: 820px;
    }
    #contrmap>div{
        display: flex;
        color: whitesmoke;
        flex-direction: column;
    }
    #contrmap>div>div{
        display: flex;
    }
    #contrmap p{
        margin-right: 40px;
    }
    select{
        width: 100px;
        height: 20px;
        border: 2px whitesmoke solid;
        background: none;
        color: whitesmoke;
    }
    select:focus{
        outline: none;
    }
    select option{
        color: black;
    }
    input{
        width: 100px;
        border: 2px whitesmoke solid;
        background: none;
        color: whitesmoke;
    }
    input:focus{
        outline: none;
    }
    #finaldone{
        background-color: #373c47;
    }
    #above_mul{
        position: absolute;
    }
    #de_mul{
        z-index: 2;
        pointer-events: none;
    }
    #text_devip{
        color: whitesmoke;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    #text_devip input{
        width: 40px;
        margin-top: 5px;
    }
    #de_chart_div{
        display: flex;
        justify-content: center;
        margin: 30px 0;
    }
    #chart_de_first{
        border: 2px white solid;
    }
</style>


