<template>
  <div class="hello">
    <div id="conn">

    <div id="error">
      <div>
        <div id="beww">
          <ion-icon name="document"></ion-icon>
          <p>喔唷,{{gh}}崩溃啦!</p>
          <span>显示此网页时出了点问题</span>
          <span>错误代码：#HN*W$Y@%O</span>
          <a href="https://github.com/zbcaoo" target="_blank">了解详情</a>
        </div>
        <div id="bewww">
          <button id="cxjz" @click="lll">重新加载</button>
        </div>
      </div>
    </div>

    <canvas id="hellocanvas"></canvas>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      gh: "",
      ghh: 0,
      oo:0
    }
  },
  mounted(){




    var hellocanvas = document.getElementById('hellocanvas')
    var conn = document.getElementById('conn')
    hellocanvas.width = conn.offsetWidth
    hellocanvas.height = conn.offsetHeight
    var left = document.getElementById('left')
    var ctxh = hellocanvas.getContext('2d')
    var plusnumx = 150;
    var plusnumy = 150;

    this.oo = function miaoe(){
      hellocanvas.width = conn.offsetWidth
      hellocanvas.height = conn.offsetHeight
      animate()
    }

    window.addEventListener('resize', this.oo, false)

    conn.addEventListener('mousemove', (e) => {
      animate()
      var x = e.clientX - left.offsetWidth
      var y = e.clientY

      var xx = (x/150|0) * 150
      var yy = (y / 150 | 0) * 150

      ctxh.fillStyle='rgb(255,255,255,0.01)'
      ctxh.fillRect(xx, yy, plusnumx, plusnumx)


      ctxh.beginPath()
      ctxh.moveTo(xx, yy)
      ctxh.lineTo(x, y)
      // 'rgb(68, 115, 255,' + (1 - Math.sqrt(Math.pow(xx - x,2) + Math.pow(yy - y,2)) / (plusnumx * Math.sqrt(2))) + ')'
      var ji1 = ctxh.createLinearGradient(xx, yy, x, y)
      ji1.addColorStop(0, 'rgb(25, 54, 141, 0)')
      ji1.addColorStop(1,'rgb(68, 115, 255)')
      ctxh.strokeStyle = ji1
      ctxh.stroke()

      ctxh.beginPath()
      ctxh.moveTo(xx + plusnumx, yy)
      ctxh.lineTo(x, y)
      var ji2 = ctxh.createLinearGradient(xx+plusnumx, yy, x, y)
      ji2.addColorStop(0, 'rgb(25, 54, 141, 0)')
      ji2.addColorStop(1,'rgb(68, 115, 255)')
      ctxh.strokeStyle = ji2
      ctxh.stroke()

      ctxh.beginPath()
      ctxh.moveTo(xx, yy + plusnumx)
      ctxh.lineTo(x, y)
      var ji3 = ctxh.createLinearGradient(xx, yy+plusnumx, x, y)
      ji3.addColorStop(0, 'rgb(25, 54, 141, 0)')
      ji3.addColorStop(1,'rgb(68, 115, 255)')
      ctxh.strokeStyle = ji3
      ctxh.stroke()

      ctxh.beginPath()
      ctxh.moveTo(xx + plusnumx, yy + plusnumx)
      ctxh.lineTo(x, y)
      var ji4 = ctxh.createLinearGradient(xx+plusnumx, yy+plusnumx, x, y)
      ji4.addColorStop(0, 'rgb(25, 54, 141, 0)')
      ji4.addColorStop(1,'rgb(68, 115, 255)')
      ctxh.strokeStyle = ji4
      ctxh.stroke()
    })

    

    function animate(){
      ctxh.clearRect(0, 0, hellocanvas.width, hellocanvas.height);
      for (let y = 0; y <= hellocanvas.height / plusnumy; y++) {
        ctxh.beginPath()
        ctxh.moveTo(0, y * plusnumy)
        ctxh.lineTo(hellocanvas.width, y * plusnumy)
        ctxh.strokeStyle = 'rgb(255,255,255,0.1)';
        ctxh.stroke()
      }
      for (let x = 0; x <= hellocanvas.width / plusnumx; x++){
        ctxh.beginPath()
        ctxh.moveTo(x * plusnumx, 0)
        ctxh.lineTo(x * plusnumx, hellocanvas.height)
        ctxh.strokeStyle = 'rgb(255,255,255,0.1)';
        ctxh.stroke()
          
        for (let y = 0; y <= hellocanvas.height / plusnumy; y++){
          

          ctxh.beginPath()
          ctxh.moveTo(x * plusnumx - 8, y * plusnumy)
          ctxh.lineTo(x * plusnumx + 8, y * plusnumy)
          ctxh.strokeStyle = 'rgb(255,255,255,0.2)';
          ctxh.stroke()
          ctxh.fillRect(x * plusnumx - 1 + plusnumx / 2, y * plusnumy - 1, 2, 2)
          ctxh.fillRect(x * plusnumx - 1, y * plusnumy - 1 + plusnumy / 2, 2, 2)
          ctxh.fillRect(x * plusnumx - 1 + plusnumx / 2, y * plusnumy - 1 + plusnumy/2, 2, 2)
          ctxh.fillStyle = 'rgb(68, 115, 255,0.4)';
          ctxh.fill()

          ctxh.beginPath()
          ctxh.moveTo(x * plusnumx, y * plusnumy - 8)
          ctxh.lineTo(x * plusnumx, y * plusnumy + 8)
          ctxh.strokeStyle = 'rgb(255,255,255,0.2)';
          ctxh.stroke()
        }
      }

    }
    animate()


    
  }, methods: {
    lll() {
      this.ghh++
      if (this.ghh > 9) {
        this.gh =this.gh.replace(/叒/, "叕")
      }
      else if (this.ghh >6 ) {
        this.gh =this.gh.replace(/双/, "叒")
      }
      else if (this.ghh > 3) {
        this.gh =this.gh.replace(/又/, "双")
      } else {
        this.gh += "又"
      }
    }
  }, beforeDestroy() {
    window.removeEventListener('resize',this.oo, false)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @media screen and (max-device-width: 825px){
  #conn{
    overflow: hidden;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
   @media screen and (min-device-width: 825px){
    /* #show{
    display: none;
  } */
  #conn{
    overflow: hidden;
    width: 100vw;
    height: 100vh;
    position: relative;
  }
   }
   .hello{
    height: 100%;
    overflow: hidden;
    position: relative;
    background-color: var(--black);
   }
  #back-img1{
    object-fit: cover;
    user-select: none;
  }
  .flex{
    display: flex;
    justify-content: center;
  }
  #back-img2{
    position: absolute;
    bottom: -160px;
    user-select: none;
    pointer-events: none;
    opacity: 1;
    transform: scaleX(1.1);
    filter: brightness(2);
  }
#back-img4,#back-img5{
    max-width: 100%;
  }
  #back-img4{
    /* animation: yelloww 1s forwards; */
    position: absolute;
    bottom: -20px;
    left: 20px;
    transform: scaleX(1.5);
  }
  #back-img5{
    /* animation: redd 1s forwards; */
    position: absolute;
    bottom: -35px;
    left: -10px;
    transform: scaleX(1.3);
  }
  #error{
    width: 100%;
    position: absolute;
    height: 500px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-60%);
    display: flex;
    justify-content: center;
    z-index: 3;
    }
    button{
      pointer-events: all;
    }
    #error>div{
      height: 100%;
      width: 650px;
      padding: 10px;
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: space-between;
      max-width: 90%;
    }
    #error>div p{
      color: var(--white);
      font-size: 1.4em;
      max-height: 18px;
      height: fit-content;
      display: inline-block;
    }
    #error>div span{
      color: rgb(134, 133, 133);
    }
    #error>div a{
      color: #4473ff;
      width: fit-content;
      cursor: pointer;
      pointer-events: all;
    }
    #error>div ion-icon{
      font-size: 40px;
      padding: 0;
      margin: 0;
      color: rgb(134, 133, 133);
    }
    #beww{
      height: 60%;
      width: fit-content;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
    }
    #bewww{
      height: 60%;
      width: fit-content;
      display: flex;
      align-items: flex-end;
      padding: 20px;
      box-sizing: border-box;
    }
    #cxjz{
      padding: 5px 10px;
      appearance: none;
      background-color: rgb(8, 100, 192);
      border-radius: 5px;
      color: var(--white);
      cursor: pointer;
      border: none;
    }
    #cxjz:active{
      background-color: rgb(16, 105, 239);
    }
    #hellocanvas{
      position: absolute;
      bottom: 0;
      user-select: none;
      pointer-events: none;
    }
    #hellocanvas::selection{
      background-color: rgba(0, 218, 218, 0);
    }

  @keyframes redd{
    0%{
      position: absolute;
      bottom: 100%;
      opacity: 0;
    }
    15%{
      position: absolute;
      bottom: 0;
      transform: translateY(100px);
      opacity: 0.5;
    }24%{
      position: absolute;
      bottom: 0;
      transform: translateY(30px);
      opacity: 0.4;
    }40%{
      transform: translateY(20px) translateX(-20px);
    }60%{
      position: absolute;
      bottom: 0;
      transform: translateY(35px) translateX(30px);
      opacity: 0.2;
    }100%{
      position: absolute;
      bottom: 0;
      transform: translateY(32px) translateX(-5px);
    }
  }
  @keyframes yelloww{
    0%{
      position: absolute;
      bottom: 100%;
      opacity: 0;
      filter: brightness(3);
    }
    10%{
      position: absolute;
      bottom: 0;
      transform: translateY(100px);
      opacity: 0.5;
    }24%{
      position: absolute;
      bottom: 200%;
      transform: translateY(30px);
      filter: brightness(2);
    }25%{
      position: absolute;
      transform: translateY(30px) translateX(10px);
      bottom: 0;
    }40%{
      position: absolute;
      bottom: 200%;
      transform: translateY(30px) translateX(-5px);
    }50%{
      position: absolute;
      bottom: 0;
      transform: translateY(35px) translateX(10px);
    }100%{
      position: absolute;
      bottom: 0;
      transform: translateY(35px) translateX(10px);
    }
  }
  .divback{
    position: absolute;
    min-width: 100%;
    min-height: 100%;
    max-width: 100%;
    max-height: 100%;
    z-index: 2;
  }
  #hover-cur:hover{
    cursor: auto;
  }
  @-webkit-keyframes  rise{
    from{
      transform: translateY(100px);
      opacity: 0;
    }to{
      transform: translateY(40px);
      opacity: 1;
    }
  }
  @keyframes rise{
    from{
      transform: translateY(100px);
      opacity: 0;
    }to{
      transform: translateY(40px);
      opacity: 1;
    }
  }
</style>



